import { ChangeDetectorRef, Component, EventEmitter, HostListener, Output } from '@angular/core';
import { CafeFnbUI } from '../../../../cafe-fnb';
import { VhAlgorithm, VhAuth, VhEinvoice, VhQuery, VhQueryCafe } from 'ionic-vhframeworks';
import { Router } from '@angular/router';
import { Platform } from '@ionic/angular';
import { LanguageService } from 'src/app/cafe/services/language.service';
import { FunctionService } from 'src/app/cafe/services/function.service';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { NzModalService } from 'ng-zorro-antd/modal';
import { NzMessageService } from 'ng-zorro-antd/message';
import { AreaService } from 'src/app/cafe/services/area.service';
import { ThemeService } from 'src/app/cafe/services/theme.service';
import { UserService } from 'src/app/cafe/services/user.service';
import { CafeFnbSharedService } from '../../../../cafe-fnb-shared.service';
import { BillService } from 'src/app/cafe/services/bill.service';
import { ProductService } from 'src/app/cafe/services/product.service';
import { PrintService } from 'src/app/cafe/services/print.service';
import { CustomerService } from 'src/app/cafe/services/customer.service';
import { NzPopconfirmDirective } from 'ng-zorro-antd/popconfirm';


interface Denomination {
  value: number;
  label: string;
}

@Component({
  selector: 'app-modal-payment',
  templateUrl: './modal-payment.component.html',
  styleUrls: ['./modal-payment.component.scss']
})
export class ModalPaymentComponent extends CafeFnbUI {
  @Output() onClose = new EventEmitter<any>();
  @Output() onSave = new EventEmitter<any>();

  constructor(
    public vhQueryCafe: VhQueryCafe,
    protected router: Router,
    public vhAuth: VhAuth,
    protected platform: Platform,
    protected languageService: LanguageService,
    protected vhComponent: VhComponent,
    protected functionService: FunctionService,
    public cdRef: ChangeDetectorRef,
    public vhAlgorithm: VhAlgorithm,
    protected nzModalService: NzModalService,
    protected nzMessageService: NzMessageService,
    public areaService: AreaService,
    public themeService: ThemeService,
    public userService: UserService,
    public cafeSapoSharedService: CafeFnbSharedService,
    public billService: BillService,
    public productService: ProductService,
    protected printService: PrintService,
    public vhQuery: VhQuery,
    public customerService: CustomerService,
    protected vhEinvoice: VhEinvoice,
  ) {
    super(
      vhQueryCafe, router, vhAuth, platform, languageService, vhComponent, functionService, cdRef, vhAlgorithm,
      nzModalService, nzMessageService, areaService, themeService, userService, cafeSapoSharedService, billService,
      productService, printService, vhQuery, customerService, vhEinvoice
    );
  }

  listWallet = [];
  denominations: Denomination[] = [
    { value: 100000, label: '100,000' },
    { value: 50000, label: '50,000' },
    { value: 10000, label: '10,000' },
    { value: 5000, label: '5,000' },
  ];
  suggestedDenominations: number[] = [];

  ngOnInit(): void {
    this.listWallet = this.vhQueryCafe.getlocalWallets()
      .filter(wallet => wallet._id != 'id_cash' &&
        wallet.id_branch == this.vhQueryCafe.getDefaultBranch()._id
      ).map(wallet => {
        return { label: wallet.name, value: wallet._id, isLeaf: true };
      });

    if (this.selectedBill.getIdWallet()) {
      this.current_id_wallet = this.selectedBill.getIdWallet();
    }
    this.invoice_cash = this.selectedBill.getCash().toString();
    this.handleGetNewDenominations();
    if (this.customerType === 2) {
      this.customerSearchList = this.vhQueryCafe.getlocalCustomers().filter(item => item._id != 'id_retail');
    }
  }

  ngAfterViewInit() {
    // Cleave js
    if (document.querySelector(".customerPay")) this.moneyEdit['cash'] = this.vhAlgorithm.vhnumeral(".customerPay input");
    if (document.querySelector(".payment-price")) this.moneyEdit['payment'] = this.vhAlgorithm.vhnumeral(".payment-price input");
  }

  ngOnDestroy() {
    this.onClose.emit();
  }

  listPaymentMethod = [
    { value: 1, name: this.languageService.translate("Cash"), image: './assets/img/tienmat.png' },
    { value: 3, name: this.languageService.translate("Bank transfer"), image: './assets/img/chuyenkhoan.png' },
    { value: 2, name: this.languageService.translate("Debit"), image: './assets/img/congno.png' },
  ];

  @HostListener('window:keydown', ['$event'])
  keyEvent(event: KeyboardEvent) {
    if (["F1"].includes(event.key)) {
      switch (event.key) {
        case "F1": document.getElementById("btn-payment").click(); break;
      }
      event.preventDefault();
      event.stopPropagation();
    }
  }

  showConfirm(popconfirm: NzPopconfirmDirective): void {
    if (popconfirm) {
      popconfirm.show();
    }
  }

  /**
   * Khi thay i phng thc thanh ton
   * @param event value ca cascader c chn. V d: nu khng c children th l [1] hoc [2], nu c children th l [3, id_wallet]
   */
  onChangePaymentType(event) {
    this.setPaymentType(event);
  }

  /**
   * Cp nht payment_type cho bill
   * @param type 1-cash-Tin mt, 2-debit-Cng n, 3-account-Ti khon
   * @param id_wallet id ca Ti khon
   * @example this.setPaymentType(1);
   */
  setPaymentType(type: 1 | 2 | 3) {
    this.selectedBill.setPaymentType(type);

    switch (type) {
      case 1: {
        this.selectedBill.setIdWallet('id_cash');
        this.invoice_payment = this.selectedBill.getPayment().toString();
        this.invoice_cash = this.selectedBill.getCash().toString();
        break;
      }
      case 2: {
        this.selectedBill.setIdWallet("");
        this.selectedBill.setCash(0);
        this.selectedBill.setPayment(0);
        this.invoice_cash = '0';
        this.invoice_payment = this.selectedBill.getPayment().toString();
        break;
      }
      case 3: {
        this.selectedBill.setIdWallet(this.listWallet[0].value);
        this.current_id_wallet = this.listWallet[0].value;
        this.invoice_payment = this.selectedBill.getPayment().toString();
        this.invoice_cash = this.selectedBill.getCash().toString();
        break;
      }
    };
    this.updateInvoice(true);
  }

  current_id_wallet;

  changeIdWallet(ev) {
    this.current_id_wallet = ev[0];
    this.selectedBill.setIdWallet(this.current_id_wallet);

  }

  private generateSuggestedDenominations(total: number): number[] {
    const denominationValues = this.denominations.map(d => d.value);
    const subsetSums = this.getAllSubsetSums(denominationValues);
    const paymentAmounts = subsetSums.map(sum => total + sum);
    paymentAmounts.push(total); // Include the exact total
    const uniqueAmounts = Array.from(new Set(paymentAmounts))
      .filter(amount => amount >= total)
      .sort((a, b) => a - b);
    return uniqueAmounts;
  }



  private getAllSubsetSums(denominations: number[]): number[] {
    const sums = new Set<number>();
    const n = denominations.length;
    for (let i = 1; i < (1 << n); i++) { // Start from 1 to exclude empty subset
      let sum = 0;
      for (let j = 0; j < n; j++) {
        if (i & (1 << j)) {
          sum += denominations[j];
        }
      }
      sums.add(sum);
    }
    return Array.from(sums);
  }

  /**
   * Khi chn Tin mt trong Danh sch tin mt gi 
   * @param amount S tin chn
   */
  selectCash(amount) {
    if (this.selectedBill.getCash() !== amount) {
      this.invoice_cash = amount;
      this.selectedBill.setCash(parseFloat(amount));
      this.vhQueryCafe.updateBill(this.selectedBill.getID(), { cash: parseFloat(this.moneyEdit['cash'].getRawValue()) })
        .then((bool) => {
          // console.log('updateBill', bool);
        }, error => {
          this.vhComponent.alertMessageDesktop('error', this.languageService.translate("An error while updating bill"));
        });
    }
  }

  onDropdownVisibleChange(visible: boolean): void {
    if (visible) {
      if (this.customerType === 3) {
        this.customerList = this.customerSearchList = [];
      }
      else if (this.customerType === 2) {
        this.customerList = this.vhQueryCafe.getlocalCustomers().filter(item => item._id != 'id_retail');
        this.customerSearchList = this.customerList;
      }
    }
  }

  handleGetNewDenominations() {
    this.suggestedDenominations = this.generateSuggestedDenominations(this.selectedBill.getTotalATax()).slice(0, 12);
  }

  getCustomerValue(): string {
    if (this.customerType !== 1 && this.selectedBill.getIdPartner() === 'id_retail') {
      return '';
    }
    else if (this.customerType === 3) {
      return '';
    }
    else if (this.selectedBill.getIdPartner() === 'id_retail') {
      return this.getRetailName();
    }
    else {
      return this.getMemberName();
    }
  }
}
