import { Component, EventEmitter, Output } from '@angular/core';
import { CafeFnbUI } from '../../../../cafe-fnb';

@Component({
  selector: 'app-modal-edit-used-time',
  templateUrl: './modal-edit-used-time.component.html',
  styleUrls: ['./modal-edit-used-time.component.scss']
})
export class ModalEditUsedTimeComponent extends CafeFnbUI {
  
  @Output() onCancel = new EventEmitter<any>();
  @Output() onOk = new EventEmitter<any>();



  cancel() {
    this.onCancel.emit();
  }

}
