import { ChangeDetectorRef, Component, ElementRef, EventEmitter, Output, ViewChild } from '@angular/core';
import { CafeFnbUI } from '../../../../cafe-fnb';
import { VhAlgorithm, VhAuth, VhEinvoice, VhQuery, VhQueryCafe } from 'ionic-vhframeworks';
import { Router } from '@angular/router';
import { Platform } from '@ionic/angular';
import { LanguageService } from 'src/app/cafe/services/language.service';
import { FunctionService } from 'src/app/cafe/services/function.service';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { NzModalService } from 'ng-zorro-antd/modal';
import { NzMessageService } from 'ng-zorro-antd/message';
import { AreaService } from 'src/app/cafe/services/area.service';
import { ThemeService } from 'src/app/cafe/services/theme.service';
import { UserService } from 'src/app/cafe/services/user.service';
import { CafeFnbSharedService } from '../../../../cafe-fnb-shared.service';
import { BillService } from 'src/app/cafe/services/bill.service';
import { ProductService } from 'src/app/cafe/services/product.service';
import { PrintService } from 'src/app/cafe/services/print.service';
import { CustomerService } from 'src/app/cafe/services/customer.service';
import { EmployeeService } from 'src/app/cafe/services/employee.service';
import { Observable, Subscription } from 'rxjs';

@Component({
  selector: 'app-cart-option',
  templateUrl: './cart-option.component.html',
  styleUrls: ['./cart-option.component.scss']
})
export class CartOptionComponent extends CafeFnbUI {
  @ViewChild('selectEmployee', { read: ElementRef }) selectRef!: ElementRef;
  private titleSub!: Subscription;
  @Output() closeDropdown = new EventEmitter<void>();

  constructor(
    public vhQueryCafe: VhQueryCafe,
    protected router: Router,
    public vhAuth: VhAuth,
    protected platform: Platform,
    protected languageService: LanguageService,
    protected vhComponent: VhComponent,
    protected functionService: FunctionService,
    protected cdRef: ChangeDetectorRef,
    public vhAlgorithm: VhAlgorithm,
    protected nzModalService: NzModalService,
    protected nzMessageService: NzMessageService,
    public areaService: AreaService,
    public themeService: ThemeService,
    public userService: UserService,
    public cafeSapoSharedService: CafeFnbSharedService,
    public billService: BillService,
    public productService: ProductService,
    protected printService: PrintService,
    public vhQuery: VhQuery,
    public customerService: CustomerService,
    protected vhEinvoice: VhEinvoice,
    public employeeService: EmployeeService,
  ) {
    super(
      vhQueryCafe, router, vhAuth, platform, languageService, vhComponent, functionService, cdRef, vhAlgorithm,
      nzModalService, nzMessageService, areaService, themeService, userService, cafeSapoSharedService, billService,
      productService, printService, vhQuery, customerService, vhEinvoice
    );
  }

  ngOnInit(): void {
  }

  ngOnDestroy() {
    // Hy b subscription khi component b hu
    if (this.titleSub) {
      this.titleSub.unsubscribe();
    }
  }

  ngAfterViewInit() {
    // Gi hm  bt u theo di thay i trong DOM
    this.observeTextChanges();
  }

  private observeTextChanges() {
    const hostEl: HTMLElement = this.selectRef.nativeElement;

    // To mt MutationObserver  theo di s thay i trong DOM ca phn t cha
    const mutationObserver = new MutationObserver(() => {
      // Cp nht title cho phn t cha vn bn c chn
      this.updateTitleForElement('.select__text');

      // Cp nht title cho tt c cc mc trong dropdown nu chng c mt hoc c thm mi
      const optionTextEls = hostEl.querySelectorAll('.list-select--item__text');
      optionTextEls.forEach((el: HTMLElement) => this.updateTitleForElement(el));
    });

    // Theo di s thay i trong DOM ca selectEmployee, bao gm vic thm hoc xo cc phn t con
    mutationObserver.observe(hostEl, {
      childList: true,  // Theo di vic thm/xo cc phn t con
      subtree: true,    // Theo di tt c cc phn t con bn trong (bao gm cc mc trong dropdown)
      characterData: true, // Theo di s thay i trong d liu vn bn ca cc phn t
    });

    // Cp nht title cho phn t cha vn bn c chn ngay lp tc
    this.updateTitleForElement('.select__text');
  }

  // Hm gip cp nht title cho phn t
  private updateTitleForElement(selector: string | HTMLElement) {
    // Ly phn t t selector hoc t trc tip phn t HTMLElement
    const element = typeof selector === 'string' ?
      this.selectRef.nativeElement.querySelector(selector) :
      selector;
    // Nu phn t tn ti v c ni dung vn bn, th cp nht title cho n
    if (element && element.textContent) {
      const text = element.textContent.trim(); // Ly vn bn  ct b khong trng tha
      element.setAttribute('title', text); // Cp nht thuc tnh title
    }
  }

  handleDeleteBill() {
    // tin hnh xa
    this.nzModalService.confirm({
      nzTitle: `${this.languageService.translate('Are you sure to cancel this invoice')}?`,
      nzCancelText: this.languageService.translate("Cancel"),
      nzOkText: "Ok",
      nzOnOk: () => {
        this.vhComponent.showLoading('').then(() => {
          this.deleteProcess()
            .then((id) => console.log('delete invoice: ', id))
            .catch((err) => {
              console.error(err);
              this.vhComponent.showToast(
                2000,
                this.languageService.translate('Delete fail'),
                'alert-toast'
              );
            })
            .finally(() => {
              this.vhQueryCafe.closeSyncOpeningBill();
              this.vhComponent.alertMessageDesktop(
                'success',
                this.languageService.translate('Invoice deleted successfully')
              );
              this.vhComponent.hideLoading(0);
            });
        });
      },
      nzOnCancel: () => { },
    });
  }

  /**
   * M modal tch bn khi ang  trang chi tit n
   */
  async openModalSeparateTable() {
    // Nu n c khuyn mi th xa khuyn mi mi tch n
    if (this.selectedBill.isPromotion()) {
      this.nzModalService.confirm({
        nzTitle: `${this.languageService.translate('n ny ang c khuyn mi. Bn c mun hy khuyn mi  tch n khng')}?`,
        nzCancelText: this.languageService.translate("Cancel"),
        nzOkText: "Ok",
        nzOnOk: () => {
          this.vhComponent.showLoading('').then(() => {
            this.destroyBillDetails_withPromotions().then(() => {
              this.vhQueryCafe
                .updateBill_Billdetail(
                  this.selectedBill.getID(),
                  this.selectedBill.getCreateUpdateInvoice(5),
                  this.selectedBill.getCreateUpdateInvoiceDetail()
                )
                .then(() => {
                  this.getBill_Detail_ByID_Bill();
                })
                .catch((err) => console.error(err))
                .finally(() => {
                  this.vhComponent.hideLoading(0);
                  this.showModalSeparateTable = true;
                  this.closeDropdown.emit();
                });
            });
          });
        },
        nzOnCancel: () => { },
      });
    }
    else {
      this.showModalSeparateTable = true;
    }
  }
}
