import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { VhAuth, VhQueryCafe } from 'ionic-vhframeworks';
import { VhComponent } from '../components/vh-component/vh-component';
import { LanguageService } from './language.service';
import { DateService } from './date.service';
import { NzModalService } from 'ng-zorro-antd/modal';


@Injectable({
  providedIn: 'root'
})
export class UserService {

  constructor(
    private vhAuth: VhAuth,
    private router: Router,
    private vhComponent: VhComponent,
    private languageService: LanguageService,
    private vhQueryCafe: VhQueryCafe,
    private dateService: DateService,
    private nzModalService: NzModalService,
  ) {}


  
  /**
   * Kim tra xem ti khon  ht hn cha.
   * Nu > 3 ngy th check xem cn trong hn c php s dng hay khng, 
   * nu trong hn th hin thng yu cu gia hn, 
   * ngc li thng bo ti khon  tm kha
   */
  checkExpireAccount() {
    let number = this.dateService.getNumberOfDay(new Date(), new Date(this.vhQueryCafe.getLocalRestrictionMainPackage().expire_date));

    if (number > 3) { // check ht hn sau 3 ngy th hin thng bo
      // check tip  cn trong hn c php s dng hay ko
      if (number < this.vhQueryCafe.getLocalRestrictionMainPackage().extra_usage_duration) {
        this.nzModalService.confirm({
          nzTitle: this.languageService.translate('Branch') + ': ' + this.vhQueryCafe.getDefaultBranch().name + ' ' + this.languageService.translate('has expired') + '. ' + this.languageService.translate('Please renew to continue') + '!',
          nzOkText: this.languageService.translate('Pay'),
          nzCancelText: this.languageService.translate('Continue'),
          nzCloseIcon: null,
          nzClosable: false,
          nzOnOk: () => {
            // go to thanh ton
            this.vhQueryCafe.getPayments(
              { id_store: { $eq: this.vhAuth.getUser()._id }, payment_type: { $eq: 5 } },
              {},
              {},
              0
            ).then((payments: any) => {
              if (payments) {
                if (payments.length > 0) {
                  this.router.navigate(['/cafe/payment/detail'],
                    {
                      state: payments
                    });
                } else if (payments == 0) {
                  this.router.navigate(['/cafe/payment']);
                }
              }
            })
          },
          nzOnCancel: () => {
          }
        })
      }
      else {
        this.nzModalService.confirm({
          nzTitle: this.languageService.translate('Branch') + ': ' + this.vhQueryCafe.getDefaultBranch().name + ' ' + this.languageService.translate('has expired') + '. ' + this.languageService.translate('Please renew to continue') + '!',
          nzOkText: this.languageService.translate('Pay'),
          nzCancelText: this.languageService.translate('Continue'),
          nzCloseIcon: null,
          nzClosable: false,
          nzOnOk: () => {
            // go to thanh ton
            this.vhQueryCafe.getPayments(
              { id_store: { $eq: this.vhAuth.getUser()._id }, payment_type: { $eq: 5 } },
              {},
              {},
              0
            ).then((payments: any) => {
              if (payments) {
                if (payments.length > 0) {
                  this.router.navigate(['/cafe/payment/detail'],
                    {
                      state: payments
                    });
                } else if (payments == 0) {
                  this.router.navigate(['/cafe/payment']);
                }
              }
            });
          },
          nzOnCancel: () => {
            this.router.navigate(['/login']);
          }
        });
      }
    }
  }



  /**
   * ng xut khi app v navigate v trang login
   */
  logout() {
    this.vhAuth.signOut().then(() => {
      this.vhQueryCafe.closeSyncCollections();
      this.router.navigate(['/login']);
    }, error => {
      console.error('error', error);
    });
  }



  /**
   * Chuyn qua Trang qun tr v cp nht li default_display_page ca user
   * @param type s chuyn qua trang no. /cafe/${type}
   * @param permission
   * @example this.updateInterface('dashboard', 'admin_display_page');
   */
  updateInterface(type, permission) {
    if (this.vhAuth.checkMyPermission(permission)) {
      Promise.all([
        this.vhComponent.showLoading(this.languageService.translate("Changing interface..."), "current-loading", null, null, false),
        this.vhAuth.updateMyPermission({ default_display_page: permission }),
      ]).then(() => {
        this.vhComponent.hideLoading(0);
        setTimeout(() => {
          this.router.navigate(['/cafe/' + type]);
        }, 200);
      });
    }
  }



  /**
   * Thit lp giao din trang bn hng mc nh khi m app.
   * Ly t template trong main_sector. sales-pos, sales-desktop, sales-retail,...
   * @param default_selling_page trang bn hng mc nh; sales-pos, sales-desktop, sales-retail,...
   */
  updateSalesInterface(default_selling_page) {
    this.vhComponent.showLoading('').then(() => {
      if (this.vhAuth.checkMyPermission('selling_display_page')) {
        this.vhAuth.updateMyPermission({ default_display_page: 'selling_display_page', default_selling_page : default_selling_page })
          .then((bool) => {
            this.vhComponent.hideLoading(0)
              .then(() => {
                this.router.navigate(['/cafe/' + default_selling_page], { state: { isSync: true } });
              });
          });
      }
      else {
        this.vhComponent.alertMessageDesktop("error", this.languageService.translate("You do not have this rights"));
      }
    });
  }

}
