import { VhAuth } from 'ionic-vhframeworks';
import { Component, Input, OnInit, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-config-sales',
  templateUrl: './config-sales.component.html',
  styleUrls: ['./config-sales.component.scss']
})
export class ConfigSalesComponent implements OnInit {
  best_selling: any = this.vhAuth.localStorageGET("show_best_selling");
  show_confirm_pay : any = this.vhAuth.localStorageGET("show_confirm_pay");
  @Input() showConfigSales = false;
  @Output() hide = new EventEmitter<boolean>()
  constructor(
    private vhAuth:VhAuth
  ) {  
  }

  ngOnInit(): void {
  }

  updateSetting(title: string, event) { 
    this.vhAuth.localStorageSET(title,event)
  }


}
