    ChooseFoodComponent,
    FoodDetailComponent,
    PrintOneColComponent,
    ChooseCustomerComponent,
    AddCustomerComponent,
    EditFoodComponent,
    ModalDiscountComponent,
    ModalFeeComponent,
    ModalPaymentComponent,
    ChangePriceTypeComponent,
    OtherDiscountComponent,
    ProductGiftType7Component,
    ProductGiftComponent,
