import { Injectable } from '@angular/core';
import { VhQueryCafe } from 'ionic-vhframeworks';


@Injectable({
  providedIn: 'root'
})
export class AreaService {

  /** Danh sch khu vc */
  listArea: any = this.vhQueryCafe.getlocalAreas().filter(item => item.status && item.id_branch == this.vhQueryCafe.getDefaultBranch()._id);


  constructor(
    private vhQueryCafe: VhQueryCafe,
  ) {}

}
