  getPrint() {
    electron.ipcRenderer.send("allPrint");
    electron.ipcRenderer.on("printName", (event, data) => {
      this.list_printer = data
    });
  }
  updatePrint() {
    let value = { ...this.printer }
    delete value._id;
    this.vhQueryCafe.updatePrintPage(this.printer._id, value);
  }
  printTemp() {
    let invoice = new VhOrderInvoices(this.tempBillCustomerReturn,this.bill_details_CustomerReturn,this.vhQueryCafe);
    this.vhComponent.showModal(this.renderPrintSalesPage(this.item._id), { printer: this.item, invoice: invoice }, false, false, `modal-print-${this.item['_id']}`)
  }

  private renderPrintSalesPage(type: 'print_size_a4' | 'print_size_a5' | 'print_size_k80_4c' | 'print_size_k80_3c' | 'print_size_k57_3c') {
    switch (type) {
      case 'print_size_k57_3c': return SalesK57Col3Component
      case 'print_size_k80_3c': return SalesK80Col3Component
      case 'print_size_k80_4c': return SalesK80Col4Component
      case 'print_size_a5': return SalesA5Component
      case 'print_size_a4': return SalesA4Component
      default: return SalesA4Component
    }
  }

