import { Component, ChangeDetectorRef, ElementRef, Input, Output, EventEmitter } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhAuth, VhEinvoice, VhQuery, VhQueryCafe } from 'ionic-vhframeworks';
import { BillService } from 'src/app/cafe/services/bill.service';
import { CustomerService } from 'src/app/cafe/services/customer.service';
import { PrintService } from 'src/app/cafe/services/print.service';
import { ProductService } from 'src/app/cafe/services/product.service';
import { CafeFnbSharedService } from '../../../cafe-fnb-shared.service';
import { UserService } from 'src/app/cafe/services/user.service';
import { ThemeService } from 'src/app/cafe/services/theme.service';
import { AreaService } from 'src/app/cafe/services/area.service';
import { NzModalService } from 'ng-zorro-antd/modal';
import { NzMessageService } from 'ng-zorro-antd/message';
import { FunctionService } from 'src/app/cafe/services/function.service';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { LanguageService } from 'src/app/cafe/services/language.service';
import { Platform } from '@ionic/angular';
import { CafeFnbUI } from '../../../cafe-fnb';

export interface SeparateTableInfo {
  bill: any,
  billDetail: any[],
  tableInfo: any;
}
@Component({
  selector: 'app-choose-table',
  templateUrl: './choose-table.component.html',
  styleUrls: ['./choose-table.component.scss']
})
export class ChooseTableComponent extends CafeFnbUI {

  @Input() type: 'modal' | 'page';

  @Output() onChooseTable = new EventEmitter<any>();
  @Output() onBackChooseTable = new EventEmitter<any>();

  constructor(
    public vhQueryCafe: VhQueryCafe,
    protected router: Router,
    public vhAuth: VhAuth,
    protected platform: Platform,
    protected languageService: LanguageService,
    protected vhComponent: VhComponent,
    protected functionService: FunctionService,
    protected cdRef: ChangeDetectorRef,
    public vhAlgorithm: VhAlgorithm,
    protected nzModalService: NzModalService,
    protected nzMessageService: NzMessageService,
    public areaService: AreaService,
    public themeService: ThemeService,
    public userService: UserService,
    public cafeSapoSharedService: CafeFnbSharedService,
    public billService: BillService,
    public productService: ProductService,
    protected printService: PrintService,
    public vhQuery: VhQuery,
    public customerService: CustomerService,
    protected vhEinvoice: VhEinvoice,
    public element: ElementRef,
  ) {
    super(
      vhQueryCafe, router, vhAuth, platform, languageService, vhComponent, functionService, cdRef, vhAlgorithm,
      nzModalService, nzMessageService, areaService, themeService, userService, cafeSapoSharedService, billService,
      productService, printService, vhQuery, customerService, vhEinvoice
    );
    if (this.router.getCurrentNavigation()) {
      let dataTrans: any = this.router.getCurrentNavigation().extras.state;
      this.action = dataTrans.action;
      this.id_table = dataTrans.id_table;
      this.foodToSeparate = dataTrans.foodToSeparate;
      this.prevPage = dataTrans.prevPage;
    }
  }

  listArea = [];

  action: string;
  id_table: string;
  prevPage: string;
  foodToSeparate: any;

  ngOnInit(): void {
    this.listArea = this.areaService.listArea.filter(area => area.name !== 'Take away');
    if (this.action === 'separate-table') {
      this.areaChangedForSeperateTable(this.listArea[0]._id);
    }
    else {
      this.areaChanged(this.listArea[0]._id);
    }
    this.adjustScale();
    window.addEventListener('resize', this.adjustScale.bind(this));
  }

  observerScale = new ResizeObserver(() => {
    this.adjustScale();
  });

  // Hm thay i zoom  full screen
  adjustScale() {
    const container = this.element.nativeElement.children[0];
    if (!container) return;
    const viewportHeight = window.innerHeight;
    const containerHeight = container.offsetHeight;
    const scaleY = viewportHeight / containerHeight;
    container.style.zoom = scaleY;
    if (container) {
      this.observerScale.observe(container);
    }
  }

  goBack() {
    if (this.type === 'modal') {
      this.onBackChooseTable.emit();
    }
    else {
      if (this.prevPage === 'cart-detail') {
        this.gotoOrder();
      }
      else {
        this.router.navigate(['/cafe/cafe-fnb'], { state: this.dataRestore });
      }
    }
  }

  async handleChooseTable(table) {
    if (this.type === 'modal') {
      this.onChooseTable.emit(table);
    }
    else {
      switch (this.action) {
        case 'create-bill':
          if (!table.status) {
            this.selectedTableID = table._id;
            this.billService.createBill(this.selectedAreaID, this.selectedTableID, this.searchValueAreaGoHome, this.indexTabArea, this.filterType);
          }
          break;

        case 'switch-table':
          if (this.id_table) {
            const fromTable = this.vhQueryCafe.getlocalTable(this.id_table);
            if (fromTable._id !== table._id) {
              this.selectedBill = null;
              this.switchTable(fromTable, table);
            }
          }
          break;

        case 'separate-table':
          if (this.id_table && this.foodToSeparate) {
            const table1 = this.vhQueryCafe.getlocalTable(this.id_table);
            const billTable2 = await this.vhQueryCafe.getBills_byFields({ id_table: { $eq: table._id }, bill_type: { $eq: 5 }, id_branch: { $eq: this.vhQueryCafe.getDefaultBranch()._id } }) as any;
            if (billTable2.length && billTable2[0].is_promotion) {
              this.nzModalService.confirm({
                nzTitle: `${this.languageService.translate('n ny ang c khuyn mi. Bn c mun hy khuyn mi  tch n khng')}?`,
                nzCancelText: this.languageService.translate("Cancel"),
                nzOkText: "Ok",
                nzOnOk: () => {
                  this.initBillToSeparate(billTable2[0]).then(() => {
                    this.destroyBillDetails_withPromotions().then(() => {
                      this.vhQueryCafe
                        .updateBill_Billdetail(
                          this.selectedBill.getID(),
                          this.selectedBill.getCreateUpdateInvoice(5),
                          this.selectedBill.getCreateUpdateInvoiceDetail()
                        )
                        .then(() => {
                        })
                        .catch((err) => console.error(err))
                        .finally(() => {
                          this.vhComponent.hideLoading(0);
                          this.handleSeparateTable(table1, table);
                        });
                    });
                  });
                },
                nzOnCancel: () => { },
              });
            }
            else {
              this.handleSeparateTable(table1, table);
            }
          }
          break;

        default:
          break;
      }
    }
  }

  handleSeparateTable(table1, table2) {
    let _TABLE1_BILL: SeparateTableInfo = {
      bill: null,
      billDetail: [],
      tableInfo: null
    };
    let _TABLE2_BILL: SeparateTableInfo = {
      bill: null,
      billDetail: [],
      tableInfo: null
    };
    _TABLE1_BILL['tableInfo'] = table1;
    _TABLE2_BILL['tableInfo'] = table2;
    this.vhComponent.alertConfirm(
      this.languageService.translate("Confirm"),
      "",
      this.languageService.translate("Separate table"),
      "OK",
      this.languageService.translate("Cancel")
    ).then(() => {
      // Show loading
      this.vhComponent.showLoading("", "transparent-loading").then(() => {

        // Step 1: Get Data
        this.getData(_TABLE1_BILL, _TABLE2_BILL)
          .then(() => {
            // Step 2: Transmit
            this.transmit(_TABLE1_BILL, _TABLE2_BILL, this.foodToSeparate);

            // Step 3: Separate
            this.separateTable(_TABLE1_BILL, _TABLE2_BILL);
          })
          .catch((err) => {
            this.vhComponent.hideLoading(0);
            this.vhComponent.showToast(2000, this.languageService.translate("Separate table failed"), "alert-toast");
          });

      });
    }, () => { });
  }

  areaChangedForSeperateTable(value) {
    if (value && this.id_table) {
      this.selectedAreaID = value;
      this.listTables = this.vhAlgorithm.sortStringbyASC(this.vhQueryCafe.getlocalTables().filter(item => item.id_area == this.selectedAreaID && item._id !== this.id_table), "name");
    }
  }

  /**ly d liu bill/detail
  * @param: d liu 2 bn cn tch
  */
  async getData(_TABLE1_BILL, _TABLE2_BILL) {
    return Promise.all([
      this.vhQueryCafe.getBills_byFields({ id_table: { $eq: _TABLE1_BILL['tableInfo']._id }, bill_type: { $eq: 5 }, id_branch: { $eq: this.vhQueryCafe.getDefaultBranch()._id } }),
      this.vhQueryCafe.getBills_byFields({ id_table: { $eq: _TABLE2_BILL['tableInfo']._id }, bill_type: { $eq: 5 }, id_branch: { $eq: this.vhQueryCafe.getDefaultBranch()._id } })
    ]).then(([table1, table2]: any[]) => {
      /** Kim tra bill mun tch c tn ti khng */
      if (!table1.length) {
        this.vhComponent.alertMessage("", "", this.languageService.translate("The invoice is not existed"), "OK").then(() => this.goBackArea());
        return;
      }

      _TABLE1_BILL['bill'] = table1[0];
      if (table2.length) _TABLE2_BILL['bill'] = table2[0];
      else {
        _TABLE2_BILL['bill'] = { ...table1[0], total: 0, payment: 0, discount: 0, fee: 0, id_table: _TABLE2_BILL['tableInfo']._id, id_area: _TABLE2_BILL['tableInfo'].id_area };
        delete _TABLE2_BILL['bill']['_id'];
      }

      if (table2.length) { // bn 2  c khch
        return Promise.all([
          this.vhQueryCafe.getBill_details_byId_bill(table1[0]._id), this.vhQueryCafe.getBill_details_byId_bill(table2[0]._id)
        ]).then(([billDetail1, billDetail2]: any[]) => {
          _TABLE1_BILL['billDetail'] = billDetail1;
          _TABLE2_BILL['billDetail'] = billDetail2;
        }).catch(err => console.error(err));
      } else { // bn 2 l bn trng
        return this.vhQueryCafe.getBill_details_byId_bill(table1[0]._id).then(billDetail1 => {
          _TABLE1_BILL['billDetail'] = billDetail1;
        }).catch(err => console.error(err));
      }
    });
  }

  /**
  * Chuyn mn t bn 1 sang bn 2
  * @param TABLE1_BILL Bill v bill detail ca bn 1
  * @param TABLE2_BILL Bill v bill detail ca bn 2
  * @param foodToSeparate Danh sch mn n v s lng cn chuyn
  */
  transmit(TABLE1_BILL: any, TABLE2_BILL: any, foodToSeparate: any) {
    for (let i in foodToSeparate) {
      let indexTable2 = TABLE2_BILL['billDetail'].findIndex(item => item._id == foodToSeparate[i]._id);
      let indexTable1 = TABLE1_BILL['billDetail'].findIndex(item => item._id == foodToSeparate[i]._id);
      // Nu mn n c topping th chuyn ton b s lng qua
      if (foodToSeparate[i].toppings && foodToSeparate[i].toppings.length) {

        // tng gi toppings
        const toppingTotal: number = foodToSeparate[i].toppings ? foodToSeparate[i].toppings.reduce((sum: number, current: any) => sum + current.price * current.quantity, 0) : 0;

        TABLE2_BILL['billDetail'].push({ ...foodToSeparate[i] });

        // tnh li tng
        TABLE1_BILL['bill'].total -= foodToSeparate[i].price * foodToSeparate[i].quantity + toppingTotal;
        TABLE2_BILL['bill'].total += foodToSeparate[i].price * foodToSeparate[i].quantity + toppingTotal;

        TABLE1_BILL['billDetail'][indexTable1].quantity = 0;
        // b qua on code di
        continue;
      }
      // dnh cho mn khng c topping
      // Nu mn n cha c sn trong n th push vo mng
      else if (indexTable2 == -1) {
        TABLE2_BILL['billDetail'].push({ ...foodToSeparate[i], quantity: foodToSeparate[i].quantity });
      }
      // Nu mn n cha c sn trong n th tng quantity
      else {
        TABLE2_BILL['billDetail'][indexTable2].quantity += foodToSeparate[i].quantity;
      }

      TABLE1_BILL['bill'].total -= foodToSeparate[i].price * foodToSeparate[i].quantity;
      TABLE2_BILL['bill'].total += foodToSeparate[i].price * foodToSeparate[i].quantity;

      TABLE1_BILL['billDetail'][indexTable1].quantity -= foodToSeparate[i].quantity;
    }
    // lc li cc mn quantity > 0
    TABLE1_BILL['billDetail'] = TABLE1_BILL['billDetail'].filter(item => item.quantity);
    TABLE2_BILL['billDetail'] = TABLE2_BILL['billDetail'].filter(item => item.quantity);
  }

  /**Tin hnh tch bn */
  separateTable(TABLE1_BILL: any, TABLE2_BILL: any) {
    //cp nht payment
    if (TABLE1_BILL['bill'].payment_type != 2) TABLE1_BILL['bill'].payment = TABLE1_BILL['bill'].total * (1 + TABLE1_BILL['bill'].tax / 100);
    if (TABLE2_BILL['bill'].payment_type != 2) TABLE2_BILL['bill'].payment = TABLE2_BILL['bill'].total * (1 + TABLE2_BILL['bill'].tax / 100);

    // x l bn 1
    const process1 = () => {
      return new Promise((resolve, rejects) => {
        for (let item of TABLE1_BILL['billDetail']) {
          item.id_bill = TABLE1_BILL['bill']._id;
        }
        this.vhQueryCafe.updateBill_Billdetail(TABLE1_BILL['bill']._id, TABLE1_BILL['bill'], TABLE1_BILL['billDetail']).then(res => resolve(res)).catch(err => rejects(err));
      });
    };

    // x l bn 2
    const process2 = () => {
      return new Promise((resolve, rejects) => {
        if (TABLE2_BILL['bill']._id) { // bn  c khch
          for (let item of TABLE2_BILL['billDetail']) {
            item.id_bill = TABLE2_BILL['bill']._id;
          }
          this.vhQueryCafe.updateBill_Billdetail(TABLE2_BILL['bill']._id, TABLE2_BILL['bill'], TABLE2_BILL['billDetail']).then(res => resolve(res)).catch(err => rejects(err));
        } else { // bn mi
          for (let item of TABLE2_BILL['billDetail']) {
            delete item.id_bill; delete item._id;
          }
          Promise.all([
            this.vhQueryCafe.createBill_Billdetail(TABLE2_BILL['bill'], TABLE2_BILL['billDetail']),
            this.vhQueryCafe.updateTable(TABLE2_BILL['bill'].id_table, { status: true })
          ]).then(res => resolve(res)).catch(err => rejects(err));
        }
      });
    };

    Promise.all([process1(), process2()]).then(res => {
      this.vhComponent.showToast(2000, this.languageService.translate("Separate table successfully"), "success-toast").then(() => {
        this.vhComponent.hideLoading(0);
        this.goBackArea();
      });
    }).catch(err => {
      console.error(err);
      this.vhComponent.hideLoading(0);
      this.vhComponent.showToast(2000, this.languageService.translate("Separate table failed"), "alert-toast");
    });
  }

  goBackArea() {
    this.router.navigate(['/cafe/cafe-fnb'], { state: this.dataRestore });
  }
}
