import { Component, Input, ChangeDetectorRef, SimpleChanges } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhAuth, VhEinvoice, VhQuery, VhQueryCafe } from 'ionic-vhframeworks';
import { BillService } from 'src/app/cafe/services/bill.service';
import { CustomerService } from 'src/app/cafe/services/customer.service';
import { PrintService } from 'src/app/cafe/services/print.service';
import { ProductService } from 'src/app/cafe/services/product.service';
import { CafeFnbSharedService } from '../../../cafe-fnb-shared.service';
import { UserService } from 'src/app/cafe/services/user.service';
import { ThemeService } from 'src/app/cafe/services/theme.service';
import { AreaService } from 'src/app/cafe/services/area.service';
import { NzModalService } from 'ng-zorro-antd/modal';
import { NzMessageService } from 'ng-zorro-antd/message';
import { FunctionService } from 'src/app/cafe/services/function.service';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { LanguageService } from 'src/app/cafe/services/language.service';
import { Platform } from '@ionic/angular';
import { CafeFnbUI } from '../../../cafe-fnb';
import { VhOrderInvoices } from 'src/app/cafe/interface/vh-order-invoice';
import { VhType } from 'src/app/cafe/interface/vh-type';

@Component({
  selector: 'app-bill-item',
  templateUrl: './bill-item.component.html',
  styleUrls: ['./bill-item.component.scss']
})
export class BillItemComponent extends CafeFnbUI {
  @Input('bill') bill;

  constructor(
    public vhQueryCafe: VhQueryCafe,
    protected router: Router,
    public vhAuth: VhAuth,
    protected platform: Platform,
    protected languageService: LanguageService,
    protected vhComponent: VhComponent,
    protected functionService: FunctionService,
    protected cdRef: ChangeDetectorRef,
    public vhAlgorithm: VhAlgorithm,
    protected nzModalService: NzModalService,
    protected nzMessageService: NzMessageService,
    public areaService: AreaService,
    public themeService: ThemeService,
    public userService: UserService,
    public cafeSapoSharedService: CafeFnbSharedService,
    public billService: BillService,
    public productService: ProductService,
    protected printService: PrintService,
    public vhQuery: VhQuery,
    public customerService: CustomerService,
    protected vhEinvoice: VhEinvoice,
  ) {
    super(
      vhQueryCafe, router, vhAuth, platform, languageService, vhComponent, functionService, cdRef, vhAlgorithm,
      nzModalService, nzMessageService, areaService, themeService, userService, cafeSapoSharedService, billService,
      productService, printService, vhQuery, customerService, vhEinvoice
    );
  }

  ngOnInit(): void {
    this.frameStyle = {
      'border-top-width': '4px',
      'border-left-width': '0px',
      'border-bottom-width': '0px',
      'border-right-width': '0px',
      'border-color': this.bill?.id_table === 'go_home' ? '#ff5900' : 'var(--ion-color-vh-green)',
      'border-style': 'solid',
    };
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes['bill']) {
      this.frameStyle = {
        'border-top-width': '4px',
        'border-left-width': '0px',
        'border-bottom-width': '0px',
        'border-right-width': '0px',
        'border-color': this.bill?.id_table === 'go_home' ? '#ff5900' : 'var(--ion-color-vh-green)',
        'border-style': 'solid',
      };
    }
  }

  dropdownVisibilityMap = new Map<string, boolean>();

  toggleDropdown(id: string): void {
    const current = this.dropdownVisibilityMap.get(id) ?? false;

    // Hide all dropdowns first
    for (let key of this.dropdownVisibilityMap.keys()) {
      this.dropdownVisibilityMap.set(key, false);
    }

    // Toggle current one
    this.dropdownVisibilityMap.set(id, !current);
  }

  onDropdownVisibilityChange(visible: boolean, id: string): void {
    this.dropdownVisibilityMap.set(id, visible);
  }

  closeDropdown(billId: string) {
    this.dropdownVisibilityMap.set(billId, false);
  }

  gotoCartDetail() {
    this.selectedBill = new VhOrderInvoices(this.bill, [], this.vhQueryCafe);
    this.selectedTableID = this.bill.id_table;
    this.areaChanged(this.bill.id_area);
    this.debt = this.bill.id_customer == 'id_retail' ? false : this.vhQueryCafe.getlocalCustomer(this.bill.id_customer);
    let dataRestore = { indexTab: this.indexTabArea, searchValue: this.searchValueAreaGoHome, filterType: this.filterType, id_area: this.selectedAreaID };
    if (this.bill.bill_type == 5) {
      this.router.navigate(['/cafe/cafe-fnb/detail'], { state: { id_table: this.selectedTableID, id_bill: this.bill._id, dataRestore } });
    }
  }

  handlePayment() {
    this.selectedBill = new VhOrderInvoices(this.bill, [], this.vhQueryCafe);
    this.selectedTableID = this.bill.id_table;
    this.areaChanged(this.bill.id_area);
    this.debt = this.bill.id_customer == 'id_retail' ? false : this.vhQueryCafe.getlocalCustomer(this.bill.id_customer);
    this.initBillToPayment(this.bill).then(() => {
      this.isShowModalPay = true;
    });
  }

  /**
   * Hm cp nht thng tin ha n sau khi chuyn bn
   * @param bill
   * @returns
   */
  initBillToPayment(bill) {
    return Promise.all([
      this.vhQueryCafe.getBill(bill._id),
      this.vhQueryCafe.getBill_details_byId_bill(bill._id),
      this.vhComponent.showLoading(''),
      this.vhQuery.getDocsByFields('barcode_designs ', {
        id_branch: { $eq: this.vhQueryCafe.getDefaultBranch()._id },
        default: { $eq: true },
      }),
      this.vhQueryCafe.getEarningPointsBills(),
      this.vhQueryCafe.getEarningPointsProducts(),
    ])
      .then(
        ([
          bill,
          billDetail,
          loading,
          label,
          earning_point_bills,
          earning_points_products,
        ]) => {
          if (label['length'] && label[0].status) this.stamp = label[0];
          this.list_earning_point_bills = earning_point_bills;
          this.list_earning_points_products = earning_points_products;

          if (!bill) {
            this.vhComponent.alertMessage('', '', this.languageService.translate('The invoice is not existed'), 'OK').then(() => this.goBack());
            return;
          }
          // Khi to i tng ha n
          this.selectedBill = new VhOrderInvoices(
            bill,
            this.vhAlgorithm.sortDatebyDESC(billDetail, 'time_start_order'),
            this.vhQueryCafe,
            this.platform
          );
          this.initCustomer();

          this.getClassAndProgram();
          // kim tra cho php n
          this.debt = this.selectedBill.getIdPartner() == 'id_retail' ? false : this.vhQueryCafe.getlocalCustomer(this.selectedBill.getIdPartner());
          this.default_price_type = this.selectedBill.getPriceType() ? this.selectedBill.getPriceType() : 1;

          this.selectedBill.setIdTable(bill['id_table']);
          this.selectedBill.setIdEmployee(this.vhAuth.getUser()._id);
          this.selectedBill.setIdArea(bill['id_area']);

          // ly thu
          let tax: any = this.vhQueryCafe.getlocalTaxs().find((tax) => tax.status && tax.default);
          if (tax) this.selectedBill.setTax(parseFloat(tax.value));

          // ly thng tin bn
          if (this.selectedBill.getIdTable() == "go_home") {
            this.tableInfo = this.languageService.translate("Take away");
          }
          else {
            this.tableInfo = `${this.vhQueryCafe.getlocalArea(this.selectedBill.getIdArea()).name} - ${this.vhQueryCafe.getlocalTable(this.selectedBill.getIdTable()).name}`;
          }

          // X l dch v thi gian
          if(this.selectedBill.getInvoiceDetail().length && this.productService.isContainServiceTime(this.selectedBill)) {
            this.endTime = new Date();
            // set li price
            this.selectedBill.getInvoiceDetail()?.forEach((item, index) => {
              if (item.ptype === VhType.SERVICETIME && !item.is_paused) {
                const newItem = { ...item, time_end_order: this.endTime };
                const serviceTimePrice: any = this.productService.calculateServiceTimePrice(newItem, this.endTime, this.selectedBill.getPriceType());
                newItem.price = serviceTimePrice.total_price;
                newItem.price_origin = serviceTimePrice.total_price;
                this.selectedBill.getInvoiceDetail()[index] = {...newItem};
              }
            });
            // Tnh li total cho bill
            this.selectedBill.setSubTotal();
          }
        }
      )
      .catch((err) => {
        console.error(err);
        this.vhComponent.alertMessageDesktop('error', this.languageService.translate('Connect error'));
      })
      .finally(() => {
        this.vhComponent.hideLoading(0);
      });
  }

  frameStyle: any = {};

  frameStyleKhuVuc: any = {
    'border-top-width': '0px',
    'border-left-width': '0px',
    'border-bottom-width': '1px',
    'border-right-width': '0px',
    'border-color': '#f4f6f8',
    'border-style': 'solid',
  };

  get _frameStyle() {
    return JSON.parse(JSON.stringify(this.frameStyle));
  }
}
