import { ChangeDetectorRef, Component, EventEmitter, Input, Output } from '@angular/core';
import { CafeFnbUI } from '../../../../cafe-fnb';
import { VhAlgorithm, VhAuth, VhEinvoice, VhQuery, VhQueryCafe } from 'ionic-vhframeworks';
import { Router } from '@angular/router';
import { Platform } from '@ionic/angular';
import { LanguageService } from 'src/app/cafe/services/language.service';
import { FunctionService } from 'src/app/cafe/services/function.service';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { NzModalService } from 'ng-zorro-antd/modal';
import { NzMessageService } from 'ng-zorro-antd/message';
import { AreaService } from 'src/app/cafe/services/area.service';
import { ThemeService } from 'src/app/cafe/services/theme.service';
import { UserService } from 'src/app/cafe/services/user.service';
import { CafeFnbSharedService } from '../../../../cafe-fnb-shared.service';
import { BillService } from 'src/app/cafe/services/bill.service';
import { ProductService } from 'src/app/cafe/services/product.service';
import { PrintService } from 'src/app/cafe/services/print.service';
import { CustomerService } from 'src/app/cafe/services/customer.service';

@Component({
  selector: 'app-modal-discount',
  templateUrl: './modal-discount.component.html',
  styleUrls: ['./modal-discount.component.scss']
})
export class ModalDiscountComponent extends CafeFnbUI {
  @Output() onClose = new EventEmitter<any>();

  constructor(
    public vhQueryCafe: VhQueryCafe,
    protected router: Router,
    public vhAuth: VhAuth,
    protected platform: Platform,
    protected languageService: LanguageService,
    protected vhComponent: VhComponent,
    protected functionService: FunctionService,
    protected cdRef: ChangeDetectorRef,
    public vhAlgorithm: VhAlgorithm,
    protected nzModalService: NzModalService,
    protected nzMessageService: NzMessageService,
    public areaService: AreaService,
    public themeService: ThemeService,
    public userService: UserService,
    public cafeSapoSharedService: CafeFnbSharedService,
    public billService: BillService,
    public productService: ProductService,
    protected printService: PrintService,
    public vhQuery: VhQuery,
    public customerService: CustomerService,
    protected vhEinvoice: VhEinvoice,
  ) {
    super(
      vhQueryCafe, router, vhAuth, platform, languageService, vhComponent, functionService, cdRef, vhAlgorithm,
      nzModalService, nzMessageService, areaService, themeService, userService, cafeSapoSharedService, billService,
      productService, printService, vhQuery, customerService, vhEinvoice
    );
  }

  /** Danh sch loi gim gi */
  listOptionDiscount = [
    { label: '%', value: 'percent', isSelect: true },
    { label: this.languageService.translate('Value'), value: 'number', isSelect: false },
  ];
  /** Loi gim gi ang chn */
  selectedDiscountType: 'percent' | 'number' = 'percent';
  /** Bin tm  lu % gim gi */
  tempPercentDiscount: number;
  /** Bin tm  lu Gi tr gim gi */
  tempDiscount: number;

  ngOnInit(): void {
    // Khi m modal th ly li d liu % v gi tr gim gi
    this.tempPercentDiscount = this.percent_discount;
    this.tempDiscount = this.selectedBill.getDiscount();
  }

  ngAfterViewInit() {
    // Cleave js
    if (document.querySelector(".discount-price")) this.moneyEdit['discount-price'] = this.vhAlgorithm.vhnumeral(".discount-price input");
  }

  /**
   * Khi reset gi tr Gim gi
   */
  resetDiscount() {
    this.tempPercentDiscount = 0;
    this.tempDiscount = 0;
  }

  /**
   * Khi thay i % gim gi.
   *
   * m bo gi tr nhp vo khng qu 100 v l s thc.
   *
   * @param event gi tr nhp vo
   */
  onPercentDiscountPriceChange(event: any) {
    // Ch cho php nhp s v du chm
    let value = event.replace(/[^0-9.]/g, '');
    // Ch cho php nhp mt du chm
    const decimalIndex = value.indexOf('.');
    if (decimalIndex !== -1) {
      value = value.slice(0, decimalIndex + 1) + value.slice(decimalIndex + 1).replace(/\./g, '');
    }
    // Khi ch nhp du chm th t ng thm s 0 trc
    if (value === '.') {
      value = '0.';
    }
    // Gii hn gi tr nhp vo khng qu 100
    let parsedValue = parseFloat(value);
    if (!isNaN(parsedValue) && parsedValue > 100) {
      parsedValue = 100;
      value = '100';
    }
    this.tempPercentDiscount = value;
    // Update input
    const inputElement = document.querySelector('.percent-discount-price input') as HTMLInputElement;
    if (inputElement) {
      inputElement.value = value;
    }

    this.tempDiscount = this.selectedBill.getSubTotal() * (parsedValue / 100);

    this.cdRef.detectChanges();
  }

  /**
   * Cp nht li tempPercentDiscount sau khi thay i tempDiscount
   */
  updateTempDiscountPercent() {
    let discount: any = this.moneyEdit['discount-price'].getRawValue();
    this.tempDiscount = (discount > 0 && discount < this.selectedBill.getSubTotal() ? parseFloat(discount) : 0);
    const discountPercent = (this.tempDiscount / this.selectedBill.getSubTotal()) * 100;
    this.tempPercentDiscount = discountPercent;
  }

  updateDiscountPercent() {
    this.selectedBill.setPercentDiscountBill(this.tempPercentDiscount);
    this.editPercentDiscount(this.tempPercentDiscount);
  }

  /**Cp nht gim gi */
  updateDiscount() {
    let discount: any = this.moneyEdit['discount-price'].getRawValue();
    this.selectedBill.setDiscount(discount ? parseFloat(discount) : 0);
    this.updateInvoice(true);
    this.invoice_payment = this.selectedBill.getPayment().toString();
    this.invoice_cash = this.selectedBill.getCash().toString();
    this.percent_discount = discount / this.selectedBill.getSubTotal() * 100;
  }

  handleClickSaveDiscount() {
    this.updateServiceTime();
    this.updateDiscountPercent();
    this.updateDiscount();
  }

}
