import { ChangeDetectorRef, Component, ElementRef, EventEmitter, HostListener, Input, Output } from '@angular/core';
import { ActivatedRouteSnapshot, Router, RouterStateSnapshot, UrlTree } from '@angular/router';
import { Platform } from '@ionic/angular';
import { VhAlgorithm, VhAuth, VhEinvoice, VhQuery, VhQueryCafe } from 'ionic-vhframeworks';
import { LanguageService } from 'src/app/cafe/services/language.service';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { FunctionService } from 'src/app/cafe/services/function.service';
import { NzModalService } from 'ng-zorro-antd/modal';
import { NzMessageService } from 'ng-zorro-antd/message';
import { AreaService } from 'src/app/cafe/services/area.service';
import { ThemeService } from 'src/app/cafe/services/theme.service';
import { UserService } from 'src/app/cafe/services/user.service';
import { CafeFnbUI } from 'src/app/cafe/desktop/cafe-fnb/cafe-fnb';
import { CafeFnbSharedService } from 'src/app/cafe/desktop/cafe-fnb/cafe-fnb-shared.service';
import { BillService } from 'src/app/cafe/services/bill.service';
import { ProductService } from 'src/app/cafe/services/product.service';
import { PrintService } from 'src/app/cafe/services/print.service';
import { Observable } from 'rxjs';
import printJS from 'print-js';
import { EmployeeService } from 'src/app/cafe/services/employee.service';
import { CustomerService } from 'src/app/cafe/services/customer.service';
import { VhOrderInvoices } from 'src/app/cafe/interface/vh-order-invoice';
import { VhType } from 'src/app/cafe/interface/vh-type';
declare var electron: any;

@Component({
  selector: 'app-cart-detail',
  templateUrl: './cart-detail.component.html',
  styleUrls: [
    './cart-detail.component.scss',
    './cart-detail-custom.component.scss',
  ]
})
export class CartDetailComponent extends CafeFnbUI {

  /**  trang booking hay cart-detail  hin th cc component ph hp */
  @Input() type: 'booking' | 'cart-detail' = 'cart-detail';

  @Output() bookingOnCancel = new EventEmitter<any>();
  @Output() bookingOnOk = new EventEmitter<any>();
  
  public VhType = VhType;

  constructor(
    public vhQueryCafe: VhQueryCafe,
    protected router: Router,
    public vhAuth: VhAuth,
    protected platform: Platform,
    protected languageService: LanguageService,
    protected vhComponent: VhComponent,
    protected functionService: FunctionService,
    protected cdRef: ChangeDetectorRef,
    public vhAlgorithm: VhAlgorithm,
    protected nzModalService: NzModalService,
    protected nzMessageService: NzMessageService,
    public areaService: AreaService,
    public themeService: ThemeService,
    public userService: UserService,
    public cafeSapoSharedService: CafeFnbSharedService,
    public billService: BillService,
    public productService: ProductService,
    protected printService: PrintService,
    public vhQuery: VhQuery,
    public employeeService: EmployeeService,
    public customerService: CustomerService,
    protected vhEinvoice: VhEinvoice,
    public element: ElementRef,
  ) {
    super(
      vhQueryCafe, router, vhAuth, platform, languageService, vhComponent, functionService, cdRef, vhAlgorithm,
      nzModalService, nzMessageService, areaService, themeService, userService, cafeSapoSharedService, billService,
      productService, printService, vhQuery, customerService, vhEinvoice
    );
    if (this.router.getCurrentNavigation()) {
      let dataTrans: any = this.router.getCurrentNavigation().extras.state;
      if (dataTrans.dataRestore) this.dataRestore = dataTrans.dataRestore;
      if (!this.selectedBill || this.selectedBill === null) {
        this.selectedBill = new VhOrderInvoices();
      }
      this.initInvoice2(dataTrans);
    }
    else {
      this.isLoadedInvoice2 = true;
    }
  }

  currentAction = 'chooseFood';

  @HostListener('window:keydown', ['$event'])
  keyEvent(event: KeyboardEvent) {
    const hasInvoiceDetails = this.selectedBill.getInvoiceDetail().length > 0;
    const hasIdTable = !!this.selectedBill.getIdTable();
    const canShowModalPay = hasInvoiceDetails && hasIdTable;

    if (["F1", "F2", "F3", "F4", "Delete", "Escape"].includes(event.key)) {
      switch (event.key) {
        case "F1":
          if (canShowModalPay) {
            this.isShowModalPay = true;
          }
          break;
        case "F2":
          if (this.stamp && hasInvoiceDetails) {
            this.printAllLabel();
          }
          break;
        case "F3":
          if (hasInvoiceDetails) {
            this.handlePrintTest();
          }
          break;
        case "F4":
          if (this.checkPrinterKitchen && hasInvoiceDetails) {
            this.handleEmit_printKitchenAll(true);
          }
          break;
        case "Delete":
          this.handleDeleteBill();
          break;
        case "Escape":
          if (hasInvoiceDetails) {
            this.goBack();
          } else {
            this.deleteInvoice(true);
          }
          break;
      }

      event.preventDefault();
      event.stopPropagation();
    }
  }

  handleDeleteBill() {
    // tin hnh xa
    this.nzModalService.confirm({
      nzTitle: `${this.languageService.translate('Are you sure to cancel this invoice')}?`,
      nzCancelText: this.languageService.translate("Cancel"),
      nzOkText: "Ok",
      nzOnOk: () => {
        this.vhComponent.showLoading('').then(() => {
          this.deleteProcess()
            .then((id) => console.log('delete invoice: ', id))
            .catch((err) => {
              console.error(err);
              this.vhComponent.showToast(
                2000,
                this.languageService.translate('Delete fail'),
                'alert-toast'
              );
            })
            .finally(() => {
              this.vhQueryCafe.closeSyncOpeningBill();
              this.vhComponent.alertMessageDesktop(
                'success',
                this.languageService.translate('Invoice deleted successfully')
              );
              this.vhComponent.hideLoading(0);
            });
        });
      },
      nzOnCancel: () => { },
    });
  }



  ngOnInit() {
    this.openSearchProduct([1, 3]);
    this.adjustScale();
    window.addEventListener('resize', this.adjustScale.bind(this));
    if (this.platform.is('electron')) {
      this.vhQueryCafe.connectDisplayLEDScreens();
    }
    this.list_selected_dishs = null;
  }



  ngOnDestroy() {
    if (this.type !== 'booking') this.vhQueryCafe.closeSyncOpeningBill();
    if (this.platform.is('electron')) {
      this.vhQueryCafe.disconnectDisplayLEDScreens();
    }
  }


  openSearchProduct(types: number[]) {
    this.showDrawerProductList = true;
    this.getMenu(types);
  }



  closeSearchProduct(ev) {
    if (ev) this.createBillDetail(ev);
    else this.showDrawerProductList = false;
    this.indexTab = 0;
  }


  chooseProduct(item) {
    this.closeSearchProduct(item);
  }



  chooseFood() {
    this.showOptions = false;
    if (this.choiceFoodSearch.toppings)
      this.choiceFoodSearch.toppings = this.choiceFoodSearch.toppings.filter(
        (item) => item.quantity
      );
    if (this.choiceFoodSearch.quantity) {
      if (this.choiceFoodSearch['combos']) {
        this.choiceFoodSearch['combos'] = this.choiceFoodSearch['combos'].map((item) => {
          return {
            ...item,
            id_category: this.vhQueryCafe.getlocalProduct(item.id_product)
              .id_category,
          };
        });
      }
      this.closeSearchProduct(this.choiceFoodSearch);
    }
  }


  printer_kitchens = this.vhQueryCafe.getlocalPrinters();

  id_printer_kitchen = this.vhQueryCafe.getlocalPrinters().length ? this.vhQueryCafe.getlocalPrinters().find(e => e.default)._id : '';

  printKitchenAll = false; // bin nhn gi tr output printKitchenAll t component payment-button1

  /** M trang in */
  handlePrintInvoice() {
    if (this.printer)
      this.vhQueryCafe.getBill(this.selectedBill.getID()).then((res: any) => {
        this.selectedBill.setBillCode(res.bill_code);
        this.vhComponent
          .showModal(
            this.printService.renderPrintPage(this.printer['_id']),
            { printer: this.printer, invoice: this.selectedBill },
            false,
            false,
            `modal-print-${this.printer['_id']}`
          ).then((modal) => {
            modal.onWillDismiss().then(() => {
              this.goBack();
            });
          });
      });

    else this.goBack();
  }

  goBack() {
    // ng cc modal c th ang m
    this.isShowModalEditUsedTime = false;
    this.showDrawerEdit = false;
    if (this.showModalChooseTableToSwitch) {
      this.showModalChooseTableToSwitch = false;
    }
    else {
      this.isExit = true;
      if (
        this.dataRestore['searchValueToday'] &&
        this.dataRestore['filterTypeToday']
      ) {
        delete this.dataRestore['searchValueToday'];
        delete this.dataRestore['filterTypeToday'];
      }
      this.router.navigate(['/cafe/cafe-fnb'], { state: this.dataRestore });
    }
  }



  /** M trang in th*/
  handlePrintTest() {
    if (this.checkPrinter) {
      if (this.vhAuth.checkMyPermission('sales_enable_print_out_for_customer')) {
        this.updateServiceTime();
        this.vhComponent
          .showModal(
            this.printService.renderPrintPage(this.printer['_id']),
            { printer: this.printer, invoice: this.selectedBill, temp: true },
            false,
            false,
            `modal-print-${this.printer['_id']}`
          )
          .then((modal) => {
            modal.onWillDismiss().then(() => { });
          });
      }
      else
        this.nzMessageService.error(
          this.languageService.translate('You do not have this rights'),
          { nzDuration: 5000 }
        );
    }
    else {
      this.printService.printerNotEnableNotification();
    }
  }

  checkDeactivate(
    currentRoute: ActivatedRouteSnapshot,
    currentState: RouterStateSnapshot,
    nextState?: RouterStateSnapshot
  ):
    | Observable<boolean | UrlTree>
    | Promise<boolean | UrlTree>
    | boolean
    | UrlTree {
    if (!this.isExit) {
      if (this.selectedBill.getInvoiceDetail().length) {
        Promise.all([
          this.vhComponent.showLoading('', 'transparent-loading'),
          this.vhQueryCafe.updateBill(
            this.selectedBill.getID(),
            this.selectedBill.getCreateUpdateInvoice(this.selectedBill.getBillType())
          ),
        ]).then(() => {
          this.vhComponent.hideLoading(0);
          this.isExit = true;
          this.router.navigateByUrl(nextState.url);
        });
      } else {
        this.statusInvoice.deleting = true;
        // tin hnh xa
        Promise.all([
          this.vhComponent.showLoading('', 'transparent-loading'),
          this.deleteProcess(),
        ])
          .then((id) => {
            this.isExit = true;
            this.router.navigateByUrl(nextState.url);
          })
          .catch((err) => {
            console.error(err);
            this.vhComponent.showToast(
              2000,
              this.languageService.translate('Delete fail'),
              'alert-toast'
            );
          })
          .finally(() => {
            this.vhComponent.hideLoading(0);
            this.statusInvoice.deleting = false;
          });
      }
    }
    return this.isExit;
  }



  handleOk() {
    this.showModalChangePriceType = false;
    this.changePriceType(this.default_price_type);
  }



  handleCancel() {
    this.showModalChangePriceType = false;
  }


  /**x l iu phi */
  handleDispatcher() {
    this.choiceFood.id_dispatcherzone = this.id_dispatcherzone;
    this.choiceFood.dispatcher_done = false;
    this.vhQueryCafe.updateBill_Detail(this.choiceFood._id, { id_dispatcherzone: this.id_dispatcherzone, dispatcher_done: false });
    this.show_modal_dispatcher = false;
  }



  /**
   * In bp
   */
  openPrintKitchen(detail, i) {
    // Bt in bp
    if (this.checkPrinterKitchen.enable) {
      if(detail.ptype == VhType.SERVICETIME && !detail.is_paused) {
        this.productService.updateServiceTimePrice(detail, this.selectedBill.getPriceType());
        this.selectedBill.getInvoiceDetail()[i] = detail;
        this.selectedBill.setSubTotal();
      }
      let dataKitchen: any = {
        name: detail.name,
        quantity: +detail.quantity,
        note: detail.note,
        max_quantity: +detail.quantity,
        ptype: detail.ptype,
        ratio: detail.ratio,
        units: detail.units,
      };
      if (detail.properties) dataKitchen['properties'] = detail.properties;
      if (detail.toppings) dataKitchen['toppings'] = detail.toppings.map(item => { return { ...item, max_quantity: item.quantity }; });
      if (detail.combos) dataKitchen['combos'] = detail.combos;
      this.printKitchen['value'].push(dataKitchen);
      this.printKitchen = { ...this.printKitchen, title: 'In li' };
    }
  }



  handleEmit_printKitchenAll(ev) {
    this.updateServiceTime();
    this.printKitchenAll = ev;
    if (ev) this.printAllDishToKitchen();
  }



  /**
   * In tt c mn xung bp
   */
  printAllDishToKitchen() {
    if (this.checkPrinterKitchen.enable) {
      for (let detail of this.selectedBill.getInvoiceDetail()) {
        let dataKitchen: any = {
          name: detail.name,
          quantity: +detail.quantity,
          note: detail.note,
          max_quantity: +detail.quantity,
          ptype: detail.ptype,
          ratio: detail.ratio,
          units: detail.units,
        };
        if (detail.properties) dataKitchen['properties'] = detail.properties;
        if (detail.toppings) dataKitchen['toppings'] = detail.toppings.map(item => { return { ...item, max_quantity: item.quantity }; });
        if (detail.combos) dataKitchen['combos'] = detail.combos;
        this.printKitchen['value'].push(dataKitchen);
      }
      this.printKitchen = { ...this.printKitchen, title: 'Add dishes' };
      this.showPrintKitchenAgain = true;
    }
  }




  /**In bp */
  handlePrint(value) {
    if (this.platform.is("electron")) this.printElectron(value);
    else this.printDesktop(value);
  }



  handleCancelPrint(value?) {
    this.printKitchen['value'] = [];
    if (value) this.showPrintKitchenAgain = false;
    else this.printKitchen['status'] = false;
  }



  printElectron(value) {
    let html = value ? document.getElementById("print-kitchen-again").innerHTML : document.getElementById("print-kitchen").innerHTML;
    electron.ipcRenderer.send("print-start", {
      html: html,
      deviceName: this.printer_kitchens.find(e => e._id == this.id_printer_kitchen).printer_name,
    });
    electron.ipcRenderer.on("print-done", (event, data) => {
      if (data) this.handleCancelPrint(value);
      else this.vhComponent.alertMessageDesktop('warning', this.languageService.translate('Printing failed'), 3000);
    });
  }



  /**In bng desktop */
  printDesktop(value) {
    printJS({
      printable: value ? "print-kitchen-again" : "print-kitchen",
      type: "html",
      scanStyles: false,
      style:
        'html { font-size: 14pt;}' +
        '.print-kitchen .font-bold {font-size:1.8rem; font-weight:bold}' +
        '.print-kitchen b {font-size: 14pt}' +
        '.print-kitchen .print-kitchen-font{font-size: 14pt}' +
        '.print-kitchen .print-kitchen-value { display:flex; justity-content:center: align-items:center}' +
        '.print-kitchen .print-kitchen-value-name { width : 70% ; font-size: 14pt}' +
        '.print-kitchen .print-kitchen-value-quantity { width : 30% ; font-size: 14pt ; text-align:center}' +
        '.print-kitchen .print-kitchen-value-name-note {font-style: italic; font-size: 10pt}' +
        '.print-kitchen .print-kitchen-combo { display:flex; justity-content:center: align-items:center}' +
        '.print-kitchen .print-kitchen-combo-name { width : 70% ; font-size: 12pt}' +
        '.print-kitchen .print-kitchen-properties { display:flex; justity-content:center: align-items:center}' +
        '.print-kitchen .print-kitchen-properties-name { width : 70% ; font-size: 12pt}' +
        '.print-kitchen .print-kitchen-properties-quantity { width : 30% ; font-size: 12pt ; text-align:center}' +
        '.print-kitchen .print-kitchen-topping { display:flex; justity-content:center: align-items:center}' +
        '.print-kitchen .print-kitchen-topping-name { width : 70% ; font-size: 12pt}' +
        '.print-kitchen .print-kitchen-topping-quantity { width : 30% ; font-size: 12pt ; text-align:center}'
    });
  }



  /* ---------------------------------------------------------- PAYMENT BUTTON 1 ---------------------------------------------------------- */

  observerScale = new ResizeObserver(() => {
    this.adjustScale();
  });

  // Hm thay i zoom  full screen
  adjustScale() {
    const container = this.element.nativeElement.children[0];
    if (!container) return;
    const viewportHeight = window.innerHeight;
    const containerHeight = container.offsetHeight;
    const scaleY = viewportHeight / containerHeight;
    container.style.zoom = scaleY;
    if (container) {
      this.observerScale.observe(container);
    }
  }

  selectedInvoiceDetailIndex: number | null = null;

  onSelect(index: number) {
    this.selectedInvoiceDetailIndex = index;
  }

  isShowNoteBill = false;

  /**
   * Cp nht ghi ch
   */
  updateNoteBill() {
    this.selectedBill.setNote(this.invoice_note);
    this.updateInvoice(true);
  }

  showModalDiscount = false;
  showModalFee = false;
  showPaymentInfo = false;
  showDropdownMore = false;

  onDropdownMoreChange(visible) {
    this.showDropdownMore = visible;
  }

  showChangeBillType = false;

  onDropdownChangeBill(visible) {
    this.showChangeBillType = visible;
  }

  /**
   * X l chuyn bn
   */
  handleSwitchTable(toTable) {
    // X l chuyn bn
    if (this.selectedBill.getIdTable() !== '') {
      const fromTable = this.vhQueryCafe.getlocalTable(this.selectedBill.getIdTable());
      if (fromTable._id !== toTable._id) {
        this.switchTable(fromTable, toTable);
        this.selectedBill.setIdTable(toTable._id);
        this.selectedBill.setIdArea(toTable.id_area);
        this.tableInfo = `${this.vhQueryCafe.getlocalArea(this.selectedBill.getIdArea()).name} - ${this.vhQueryCafe.getlocalTable(this.selectedBill.getIdTable()).name}`;
      }
    }
    // X l chn bn khi chuyn n t mang v sang ti bn
    else {
      if (!toTable.status) {
        this.selectedBill.setIdTable(toTable._id);
        this.selectedBill.setIdArea(toTable.id_area);
        this.tableInfo = `${this.vhQueryCafe.getlocalArea(this.selectedBill.getIdArea()).name} - ${this.vhQueryCafe.getlocalTable(this.selectedBill.getIdTable()).name}`;
        this.vhQueryCafe.updateTable(this.selectedBill.getIdTable(), { status: true });
        this.updateInvoice(true);
        this.showModalChooseTableToSwitch = false;
      }
    }
  }
}
