  /**
   * Khi to cc thng s my in
   */
  initPrinter() {
    // kim tra my in
    let printer = this.vhQueryCafe.getLocalPrintPage('page_desktop_sales');
    this.printer = printer ? printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)] : null;
    if (this.printer) {
      this.checkPrinter = this.printer.enable;
    }
    let printer_kitchen = this.vhQueryCafe.getLocalPrintPage('page_desktop_kitchen');
    this.bookingPrinterKitchen = printer_kitchen ? printer_kitchen.print_sizes[printer_kitchen.print_sizes.findIndex(e => e._id == printer_kitchen.print_size_default)] : null;
    this.checkPrinterKitchen = printer_kitchen ? printer_kitchen.print_sizes[printer_kitchen.print_sizes.findIndex((e) => e._id == printer_kitchen.print_size_default)] : { enable: false };
  }



  /**
   * M trang in
   */
  handlePrintInvoice() {
    this.vhQueryCafe.getBill(this.selectedBill.getID()).then((res: any) => {
      this.selectedBill.setBillCode(res.bill_code);
      this.vhComponent.showModal(this.printService.renderPrintPage(this.printer['_id']), { printer: this.printer, invoice: this.selectedBill }, false, false, `modal-print-${this.printer['_id']}`).then((modal) => {
        modal.onWillDismiss().then(() => { });
      });
    });
  }


  /**
   * M trang Print out for customer
   */
