  /* ---------------------------------------------------------------- BILL ---------------------------------------------------------------- */

  /**
   * ly thng tin bill theo bn
   * @param table : bn  chn
   */
  getBillByTable(table) {
    this.listTables = this.listTables.map(item => {
      if (item._id == table._id) item.selected = true;
      else item.selected = false;
      return { ...item };
    });
    this.selectedTableID = table._id;
    // get bill lu tm ca bn v
    this.vhQueryCafe.getBills_byFields({ id_table: { $eq: table['_id'] }, bill_type: { $eq: 5 }, id_branch: { $eq: this.vhQueryCafe.getDefaultBranch()._id } })
      .then((bills: VhOrderInvoices) => {
        // ly bill mi nht
        bills = this.vhAlgorithm.sortDatebyDESC(bills, 'date');
        if (bills[0]) {
          this.selectedBill = bills[0];
          this.onChangeInvoiceSelected();
          // nu bn trng m get dc bill th chuyn li sang xanh
          if (!table.status) {
            this.vhQueryCafe.updateTable(table._id, { status: true });
          }
        }
        else {
          // nu bn ang xanh m ko c bill th bo li  user reset hoc lin h CSKH
          if (table.status) {
            this.vhComponent.alertConfirm("Li", "Li truy vn ha n", " xy ra li trong qu trnh truy vn ha n, vui lng th li sau!!!");
            this.error_to_get_bill = true; // gn thnh true   component app-invoice-detail hin th thng bo
            this.vhQueryCafe.updateTable(table._id, {
              status: false,
            }).then(() => {
              this.listTables = this.listTables.map(item => {
                if (item._id == table._id) item.status = false;
                return { ...item };
              });
              this.selectedBill = null;
              this.error_to_get_bill = false;
            });
          }
          else {
            this.selectedBill = null;
            this.onChangeInvoiceSelected();
          }
        }
      }).catch((err) => {
        console.error(err);
      });
  }



