  /** Danh sch bill mang v s hin th */
  get billsGoHomeShow() {
    return this.cafeSapoSharedService.billsGoHomeShow;
  }
  /** Danh sch bill mang v s hin th */
  set billsGoHomeShow(value) {
    this.cafeSapoSharedService.billsGoHomeShow = value;
  }


  /**
   * Ly danh sch bill mang v
   */
  getBillsGoHome() {
    this.billService.getBillsGoHome()
      .then((bills: any) => {
        this.billsGoHome = JSON.parse(JSON.stringify(bills));
        this.billsGoHomeShow = this.billsGoHome;
        this.sortFilter(this.filterType);
      }).catch((err) => {
        console.error(err);
      });
  }



  sortFilter(filterType: 1 | 2 | 3 | 4) {
    this.billsGoHomeShow = this.billsGoHome;
    switch (filterType) {
      case 1: this.billsGoHomeShow.sort((prev, next) => Date.parse(next.date) - Date.parse(prev.date));; break;
      case 2: this.billsGoHomeShow.sort((prev, next) => Date.parse(prev.date) - Date.parse(next.date));; break;
      case 3: this.billsGoHomeShow.sort((prev, next) => (prev.total * (1 + prev.tax / 100)) - (next.total * (1 + next.tax / 100))); break;
      case 4: this.billsGoHomeShow.sort((prev, next) => (next.total * (1 + next.tax / 100)) - (prev.total * (1 + prev.tax / 100))); break;
    }
    this.searchBillGoHome(this.searchValueAreaGoHome);
  }



  /**
   * Tm kim bill mang v
   * @param value gi tr tm kim
   */
  searchBillGoHome(value: string) {
    this.billsGoHomeShow = this.billService.searchBillGoHome(value, this.billsGoHome);
  }



  /* -------------------------------------------------------------- CHI NHNH ------------------------------------------------------------- */

  checkBranches() {
    let branches = [...this.vhQueryCafe.getlocalBranchs()];
    let employee = this.vhAuth.getUser();
    // check branch latest
    this.branchDefault = this.vhQueryCafe.getDefaultBranch();
    let thisBranch = this.vhQueryCafe.getlocalBranch(this.vhQueryCafe.getDefaultBranch()._id);
    if (this.branchDefault.name != thisBranch.name) {
      this.vhQueryCafe.clearDefaultBranch();
      this.vhQueryCafe.setDefaultBranch(thisBranch._id);
      this.branchDefault = this.vhQueryCafe.getDefaultBranch();
    }

    if (employee['owner'] == 'boss') {
      this.branchList = branches;
    }
    else {
      this.vhAuth.getPermission(employee['_id']).then(result => {
        branches.forEach(item => {
          if (result[`${item._id}_login_branch`]) this.branchList.push(item);
        });
      });
    }
  }



