  /**
   * X l khi chn Bn
   */
  onChangeInvoiceSelected() {
    this.initInvoice();
    this.getWallets();
  }



  /**
   * Khi to ha n
   */
  initInvoice() {
    if (this.selectedBill) {
      this.selectedBill = new VhOrderInvoices(this.selectedBill, [], this.vhQueryCafe);
      this.selectedBill.setIdTable(this.selectedTableID);
      this.selectedBill.setIdEmployee(this.vhAuth.getUser()._id);
      this.selectedBill.setIdArea(this.selectedAreaID);

      // ly thu
      let tax: any = this.vhQueryCafe.getlocalTaxs().find(tax => tax.status && tax.default);
      if (tax) this.selectedBill.setTax(parseFloat(tax.value));
      if (this.selectedBill.getID()) {
        // bn  sinh bill
        Promise.all([
          this.vhQueryCafe.getBill(this.selectedBill.getID()),
          this.vhQueryCafe.getBill_details_byId_bill(this.selectedBill.getID()),
        ]).then(([bill, billDetail]) => {
          if (!bill) {
            this.nzModalService.confirm({
              nzTitle: '<i>' + this.languageService.translate("The invoice is not existed") + '</i>',
              nzIconType: "info-circle"
            });
            this.deleteBill();
            // this.vhComponent.alertMessage("", "", this.languageService.translate("The invoice is not existed"), "OK");
            return;
          }
          this.selectedBill = new VhOrderInvoices(bill, this.vhAlgorithm.sortDatebyDESC(billDetail, 'time_start_order'), this.vhQueryCafe);
          if (this.selectedBill) {
            this.percent_discount = this.selectedBill.getDiscount() / this.selectedBill.getSubTotal() * 100;
          }

          // kim tra cho php n
          this.debt = this.selectedBill.getIdPartner() == "id_retail" ? false : this.vhQueryCafe.getlocalCustomer(this.selectedBill.getIdPartner());
          this.isLoadedInvoice = true;
        }).catch(err => {
          console.error(err);
          this.nzMessageService.error(this.languageService.translate("Li kt ni"));
        });
        // ly thng tin bn
        if (this.selectedBill.getIdTable() == "go_home") {
          this.tableInfo = this.languageService.translate("Take away");
        }
        else {
          this.tableInfo = `${this.vhQueryCafe.getlocalArea(this.selectedBill.getIdArea()).name} - ${this.vhQueryCafe.getlocalTable(this.selectedBill.getIdTable()).name}`;
        }
        // kim tra my in
        this.initPrinter();
      }
      else { // neu bn cha co bill
        this.selectedBill = null;
        this.isLoadedInvoice = true;
      }
    }
  }



  /**
   * To n hng
   */
  createBill() {
    this.billService.createBill(this.selectedAreaID, this.selectedTableID, this.searchValueAreaGoHome, this.indexTabArea, this.filterType);
  }



  deleteBill() {
    this.billsGoHomeShow = this.billsGoHome.filter(item => item._id != this.selectedBill.getID());
    this.selectedBill = null;
    this.onChangeInvoiceSelected();
  }



  getWallets() {
    this.nzOptions[2].children = this.vhQueryCafe.getlocalWallets()
      .filter(wallet => wallet._id != 'id_cash' &&
        wallet.id_branch == this.vhQueryCafe.getDefaultBranch()._id
      ).map(wallet => {
        return { label: wallet.name, value: wallet._id, isLeaf: true };
      });
  }



