  /**
   * kim tra ti a 12 ky t cu cash
   * @param value
   */
  checkMaxLengthCash(value: string) {
    if (parseFloat(value) > 999999999999) {
      value = value.slice(0, 12); this.invoice_cash = value;
    }
  }



  updateCash() {
    this.selectedBill.setCash(parseFloat(this.moneyEdit['cash'].getRawValue()));
    this.vhQueryCafe.updateBill(this.selectedBill.getID(), { cash: parseFloat(this.moneyEdit['cash'].getRawValue()) })
      .then((bool) => {
        // console.log('updateBill', bool);
      }, error => {
        this.vhComponent.alertMessageDesktop('error', this.languageService.translate("An error while updating bill"));
      });
  }




  /** Thanh ton ha n
  * @param print: cho php in ha n hay k?
  */
  saveInvoice(print: 'print' | 'notPrint') {
    if (!this.vhAuth.checkLocalMyPermissionName('sales_enable_payment')) {
      this.vhComponent.alertMessageDesktop('error', this.languageService.translate('You do not have this rights'));
      return;
    }
    if (this.getTotalEarningPoint() || this.selectedBill.getUsePoint()) {
      let earned_points =
        this.getTotalEarningPoint() - this.selectedBill.getUsePoint();
      this.vhQueryCafe.updateCustomer_byEarnedPoints(
        this.selectedBill.getIdPartner(),
        earned_points
      );
      this.selectedBill.setEarningPointBill(this.getTotalEarningPoint());
    }
    // if (print == "print") {
    if (this.checkPrinter) {
      this.statusInvoice['payment'] = true;
      this.vhComponent.showLoading('').then(() => {
        const bill: any = {...this.selectedBill};
        const invoiceTemp = {...bill?.invoice};
        this.createProcess().then(async (res: any) => {
          if(this.cqt_submit_invoice_immediately) await this.handleMinvoice(invoiceTemp);
          this.nzMessageService.success(
            `${this.languageService.translate(
              'Sales invoice'
            )} ${this.selectedBill.getBillCode()} ${this.languageService.translate(
              'has been created successfully'
            )}`
          );
          this.handlePrintInvoice();
          // this.goBack(false)

          this.statusInvoice['payment'] = false;
          this.isShowModalPay = false;
          this.vhComponent.hideLoading(0);
        });
      });
    }
    else {
      this.statusInvoice['payment'] = true;
      this.vhComponent.showLoading('').then(() => {
        const bill: any = {...this.selectedBill};
        const invoiceTemp = {...bill?.invoice};
        this.createProcess().then(async (res) => {
          if(this.cqt_submit_invoice_immediately) await this.handleMinvoice(invoiceTemp);
          this.nzMessageService.success(
            `${this.languageService.translate(
              'Sales invoice'
            )} ${this.selectedBill.getBillCode()} ${this.languageService.translate(
              'has been created successfully'
            )}`
          );
          this.goBack();

          this.statusInvoice['payment'] = false;
          this.isShowModalPay = false;
          this.vhComponent.hideLoading(0);
        });
      });
    }
  }


