  /**
   * Cp nht ghi ch
   */
  updateNoteBill() {
    let note: any = document.getElementById("invoice-note");
    this.selectedBill.setNote(note.value);
    this.updateInvoice(true);
  }

  /** Thay i phng thc thanh ton
   * @param value loi thanh ton
  */
  changePaymentType(value: any) {
    this.selectedBill.setPaymentType(value[0]);
    this.selectedBill.setPayment();
    switch (value[0]) {
      case 1:
        this.selectedBill.setIdWallet("id_cash");
        this.invoice_payment = this.selectedBill.getPayment().toString();
        this.invoice_cash = this.selectedBill.getCash().toString();
        break;
      case 2:
        this.selectedBill.setIdWallet("");
        this.selectedBill.setCash(0);
        this.invoice_cash = '0';
        this.invoice_payment = this.selectedBill.getPayment().toString();
        break;

      case 3:
        this.selectedBill.setIdWallet(value[1]);
        this.invoice_payment = this.selectedBill.getPayment().toString();
        this.invoice_cash = this.selectedBill.getCash().toString();
        break;
    }

    this.updateInvoice(true);
  }


