  /* ------------------------------------------------------------ BOOKING/CART ------------------------------------------------------------ */
  getTotalFood() {
    const invoiceDetail = this.selectedBill.getInvoiceDetail();
    let total = 0;
    for (let item of invoiceDetail) {
      if (item.id_promotion) {
        total += item.products.length;
      }
      else {
        total += 1;
      }
    }

    return total;
  }



