  /**
   * Switch phng thc s gi sau khi chn bn trong modal choose-table
   *
   * Da trn bin bookingChooseTableType
   *
   * @param table bn c chn
   */
  bookingOnOkChooseTable(table) {
    switch (this.bookingChooseTableType) {
      case 'add':
        this.bookingOnChooseTable(table);
        break;
      case 'transform-to-order':
        this.transformToOrderChangeTable(table);
        break;
      case 'change':
        this.bookingChangeTable(table);
        break;
      default:
        console.log('bookingChooseTableType not found');
        break;
    }
  }



