  /**
   * Cp nht d liu ca danh sch bookingListBills v bookingListBillsShow
   */
  private updateBookingList(_id: string, updatedData: any): void {
    const updateItem = (item: any) =>
      item._id === _id ? { ...item, ...updatedData } : item;

    this.bookingListBills = this.bookingListBills.map(updateItem);
    this.bookingListBillsShow = this.bookingListBillsShow.map(updateItem);
  }



  /**
   * m li s lng bill  b lc Tt c, Sp n, Qu gi, 7 ngy tip theo
   */
  countBookingBill() {
    this.daysList.map((day) => {
      this.bookingCountBill[day.dayOfWeek] = this.countBillByDate(day.date);
    });
    this.bookingCountBill['all'] = this.bookingListBills.length;
    this.bookingCountBill['overdue'] = this.countBillByDate(this.daysList[0].date, 'overdue');
    this.bookingCountBill['upcoming'] = this.countBillByDate(this.daysList[0].date, 'upcoming');
  }



