import { ChangeDetectorRef, Component, EventEmitter, Input, Output } from '@angular/core';
import { CafeFnbUI } from '../../../../cafe-fnb';
import { VhAlgorithm, VhAuth, VhEinvoice, VhQuery, VhQueryCafe } from 'ionic-vhframeworks';
import { Router } from '@angular/router';
import { Platform } from '@ionic/angular';
import { LanguageService } from 'src/app/cafe/services/language.service';
import { FunctionService } from 'src/app/cafe/services/function.service';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { NzModalService } from 'ng-zorro-antd/modal';
import { NzMessageService } from 'ng-zorro-antd/message';
import { AreaService } from 'src/app/cafe/services/area.service';
import { ThemeService } from 'src/app/cafe/services/theme.service';
import { UserService } from 'src/app/cafe/services/user.service';
import { CafeFnbSharedService } from '../../../../cafe-fnb-shared.service';
import { BillService } from 'src/app/cafe/services/bill.service';
import { ProductService } from 'src/app/cafe/services/product.service';
import { PrintService } from 'src/app/cafe/services/print.service';
import { CustomerService } from 'src/app/cafe/services/customer.service';

@Component({
  selector: 'app-separate-table',
  templateUrl: './separate-table.component.html',
  styleUrls: ['./separate-table.component.scss']
})
export class SeparateTableComponent extends CafeFnbUI {
  @Input('prevPage') prevPage: string;

  @Output() onClose = new EventEmitter<any>();
  @Output() onSave = new EventEmitter<any>();

  constructor(
    public vhQueryCafe: VhQueryCafe,
    protected router: Router,
    public vhAuth: VhAuth,
    protected platform: Platform,
    protected languageService: LanguageService,
    protected vhComponent: VhComponent,
    protected functionService: FunctionService,
    protected cdRef: ChangeDetectorRef,
    public vhAlgorithm: VhAlgorithm,
    protected nzModalService: NzModalService,
    protected nzMessageService: NzMessageService,
    public areaService: AreaService,
    public themeService: ThemeService,
    public userService: UserService,
    public cafeSapoSharedService: CafeFnbSharedService,
    public billService: BillService,
    public productService: ProductService,
    protected printService: PrintService,
    public vhQuery: VhQuery,
    public customerService: CustomerService,
    protected vhEinvoice: VhEinvoice,
  ) {
    super(
      vhQueryCafe, router, vhAuth, platform, languageService, vhComponent, functionService, cdRef, vhAlgorithm,
      nzModalService, nzMessageService, areaService, themeService, userService, cafeSapoSharedService, billService,
      productService, printService, vhQuery, customerService, vhEinvoice
    );
  }

  foodToSeparate = []; // danh sch nhng mn n  tch sang bn khc
  listFoodChecked = [];

  ngOnInit(): void {
  }

  onSeparateQuantityChange(item: any, enteredQuantity: number) {
    const index = this.foodToSeparate.findIndex(i =>
      i._id === item._id &&
      JSON.stringify(i.toppings || []) === JSON.stringify(item.toppings || []) // match toppings if needed
    );

    // Remove if 0
    if (enteredQuantity <= 0) {
      if (index > -1) this.foodToSeparate.splice(index, 1);
      return;
    }

    const itemClone = { ...item, quantity: enteredQuantity };

    if (index > -1) {
      this.foodToSeparate[index].quantity = enteredQuantity;
    } else {
      this.foodToSeparate.push(itemClone);
    }
  }

  // Helper to get remaining quantity
  getRemainingQuantity(item: any): number {
    const match = this.foodToSeparate.find(i =>
      i._id === item._id &&
      JSON.stringify(i.toppings || []) === JSON.stringify(item.toppings || [])
    );
    return item.quantity - (match?.quantity || 0);
  }

  getSeparateQuantity(item: any): number {
    const match = this.foodToSeparate.find(i =>
      i._id === item._id &&
      JSON.stringify(i.toppings || []) === JSON.stringify(item.toppings || [])
    );
    return match?.quantity || 0;
  }

  onSelectFood(event, item) {
    const hasToppings = item.toppings && item.toppings.length > 0;

    if (event.length > 0) {
      this.listFoodChecked.push(item._id);

      //  Handle topping case: add full quantity to foodToSeparate
      const index = this.foodToSeparate.findIndex(i =>
        i._id === item._id &&
        JSON.stringify(i.toppings || []) === JSON.stringify(item.toppings || [])
      );

      if (hasToppings) {
        const fullItem = { ...item, quantity: item.quantity };

        if (index > -1) {
          this.foodToSeparate[index].quantity = item.quantity;
        } else {
          this.foodToSeparate.push(fullItem);
        }
      } else {
        // Clean up foodToSeparate for non-topping items (if already present)
        this.foodToSeparate = this.foodToSeparate.filter((food) =>
          this.listFoodChecked.includes(food._id)
        );
      }
    } else {
      this.listFoodChecked = this.listFoodChecked.filter((idFood) => idFood !== item._id);
      this.foodToSeparate = this.foodToSeparate.filter((food) =>
        this.listFoodChecked.includes(food._id)
      );
    }
  }

  ////////////////////////////////////////////////////////// Ly tn cc thng tin mn n //////////////////////////////////////////////////////////////////////////
  getNameImgFood(id_prod: string, id_subProd: string) {
    let food = this.vhQueryCafe.getlocalProduct(id_prod);
    let subFood = food.subs ? food.subs.find(item => item._id == id_subProd) : null;
    return { name: `${food ? food.name : ''} ${subFood ? '(' + this.languageService.translate(subFood.name) + ')' : ''}`, img: food.img };
  }
  getNameFood(id_prod: string, id_subProd: string) {
    let food = this.vhQueryCafe.getlocalProduct(id_prod);
    let subFood: any;
    if (id_subProd) subFood = food.subs.find(item => item._id == id_subProd);
    return `${food ? food.name : ''} ${subFood ? '(' + subFood.name + ')' : ''}`;
  }
  getKindTopping(toppings: any[]): string {
    let nameTopping: string = "";
    toppings.forEach(topping => nameTopping += `${nameTopping.length ? ", " : ""}${topping.quantity} ${this.getNameFood(topping.id_product, topping.id_subproduct)}`);
    return nameTopping;
  }
  getSubCombo(arr: any[]) {
    let subName: string = "";
    arr.forEach(item => {
      if (subName.length)
        subName += `, ${item.quantity} ${item.name}`;
      else subName += `${item.quantity} ${item.name}`;
    });
    return subName;
  }

  handleSeparate() {
    const allEmpty = this.selectedBill.getInvoiceDetail().every(billItem => {
      const remaining = this.getRemainingQuantity(billItem);
      return remaining === 0;
    });

    this.foodToSeparate = this.foodToSeparate.filter((food) => food.quantity > 0);

    if (allEmpty) {
      this.vhComponent.showToast(2000, this.languageService.translate("Can not separate all dishes"), "alert-toast");
      return;
    }

    if (this.selectedBill.getIdTable() === '') {
      this.vhComponent.showToast(2000, this.languageService.translate("No table selected"), "alert-toast");
      return;
    }

    this.router.navigate(['/cafe/cafe-fnb/choose-table'], { state: { action: 'separate-table', id_table: this.selectedBill.getIdTable(), foodToSeparate: this.foodToSeparate, prevPage: this.prevPage } });
    this.onClose.emit();
  }
}
