import { ChangeDetectorRef, Component, ElementRef } from '@angular/core';
import { CafeFnbUI } from '../cafe-fnb';
import { VhAlgorithm, VhAuth, VhEinvoice, VhQuery, VhQueryCafe } from 'ionic-vhframeworks';
import { Router } from '@angular/router';
import { Platform } from '@ionic/angular';
import { LanguageService } from 'src/app/cafe/services/language.service';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { FunctionService } from 'src/app/cafe/services/function.service';
import { NzModalService } from 'ng-zorro-antd/modal';
import { NzMessageService } from 'ng-zorro-antd/message';
import { AreaService } from 'src/app/cafe/services/area.service';
import { ThemeService } from 'src/app/cafe/services/theme.service';
import { UserService } from 'src/app/cafe/services/user.service';
import { BillService } from 'src/app/cafe/services/bill.service';
import { ProductService } from 'src/app/cafe/services/product.service';
import { PrintService } from 'src/app/cafe/services/print.service';
import { CustomerService } from 'src/app/cafe/services/customer.service';
import { CafeFnbSharedService } from '../cafe-fnb-shared.service';
import { VhOrderInvoices } from 'src/app/cafe/interface/vh-order-invoice';

@Component({
  selector: 'app-cafe-fnb-main',
  templateUrl: './cafe-fnb-main.component.html',
  styleUrls: [
    './cafe-fnb-main.component.scss',
    './cafe-fnb-main-custom.component.scss',
  ],
})
export class CafeFnbMainComponent extends CafeFnbUI {
  constructor(
    public vhQueryCafe: VhQueryCafe,
    protected router: Router,
    public vhAuth: VhAuth,
    protected platform: Platform,
    protected languageService: LanguageService,
    protected vhComponent: VhComponent,
    protected functionService: FunctionService,
    protected cdRef: ChangeDetectorRef,
    public vhAlgorithm: VhAlgorithm,
    protected nzModalService: NzModalService,
    protected nzMessageService: NzMessageService,
    public areaService: AreaService,
    public themeService: ThemeService,
    public userService: UserService,
    public cafeSapoSharedService: CafeFnbSharedService,
    public billService: BillService,
    public productService: ProductService,
    protected printService: PrintService,
    public vhQuery: VhQuery,
    public customerService: CustomerService,
    protected vhEinvoice: VhEinvoice,
    private element: ElementRef,
  ) {
    super(
      vhQueryCafe, router, vhAuth, platform, languageService, vhComponent, functionService, cdRef, vhAlgorithm,
      nzModalService, nzMessageService, areaService, themeService, userService, cafeSapoSharedService, billService,
      productService, printService, vhQuery, customerService, vhEinvoice
    );
  }

  currentTab = 1;

  listArea = [];

  ngOnInit(): void {
    this.listArea = this.areaService.listArea.filter(area => area.name !== 'Take away');
    this.onInit();
    this.adjustScale();
    window.addEventListener('resize', this.adjustScale.bind(this));
    this.getALlBills();

    // To hm callback  ng b gia cc thit b
    this.vhQueryCafe.observableSyncBillType(this.id_branch, 5).subscribe((listbill_bybilltype: any[]) => {
      if (this.isLoadedAllBill) {
        const listIdTableActive = listbill_bybilltype
          .filter(item => item.id_table !== 'go_home' && item.id_area === this.selectedAreaID)
          .map(item => item.id_table);
        // Cp nht li danh sch bill
        this.listAllBills = listbill_bybilltype.map((item) => ({
          ...item,
          areaName: this.vhQueryCafe.getlocalArea(item.id_area)?.name || 'Unknown',
          tableName: this.vhQueryCafe.getlocalTable(item.id_table)?.name || 'Unknown',
          table: this.vhQueryCafe.getlocalTable(item.id_table) || 'Unknown',
        }));

        switch (this.id_selected) {
          case 'all':
            this.listBills = this.listAllBills;
            break;
          case 'go_home':
            this.listBills = this.listAllBills.filter((bill) => bill.id_table === 'go_home');
            break;
          case 'table':
            this.listBills = this.listAllBills.filter((bill) => bill.id_table !== 'go_home');
            break;
          default:
            this.listBills = this.listAllBills.filter((bill) => bill.id_area === this.id_selected);
            break;
        }
        // Cp nht li trng thi ca bn
        this.listTables.forEach((item: any) => {
          if (listIdTableActive.includes(item._id)) {
            item.status = true;
          }
          else {
            item.status = false;
          }
        });
      }
    });
  }

  ngOnDestroy() {
    this.onDestroy();

    window.removeEventListener('resize', this.adjustScale.bind(this));
    if (this.observerScale) {
      this.observerScale.disconnect();
    }

    this.listAllBills = [];
    this.listBills = [];
    this.vhQueryCafe.closeSyncBillType();
  }

  isLoadedAllBill = false;

  getALlBills() {
    const listTables = this.vhQueryCafe.getlocalTables();
    const activeTableIds = listTables.filter(table => table.status && table._id !== 'go_home').map(table => table._id);
    const branchId = this.vhQueryCafe.getDefaultBranch()._id;

    const promises = [];

    if (activeTableIds.length > 0) {
      promises.push(
        this.vhQueryCafe.getBills_byFields({
          id_table: { $in: activeTableIds },
          bill_type: { $eq: 5 },
          id_branch: { $eq: branchId }
        }) as Promise<any[]>
      );
    } else {
      promises.push(Promise.resolve([]));
    }

    promises.push(
      this.vhQueryCafe.getBills_byFields({
        id_table: { $eq: 'go_home' },
        bill_type: { $eq: 5 },
        id_branch: { $eq: branchId }
      }) as Promise<any[]>
    );

    Promise.all(promises)
      .then(([billsFromTables, billsGoHome]) => {
        const allBills = [...billsGoHome, ...billsFromTables];
        const enrichedBills = allBills.map((item) => ({
          ...item,
          areaName: this.vhQueryCafe.getlocalArea(item.id_area)?.name || 'Unknown',
          tableName: this.vhQueryCafe.getlocalTable(item.id_table)?.name || 'Unknown',
          table: this.vhQueryCafe.getlocalTable(item.id_table) || 'Unknown',
        }));

        this.billsGoHome = [...billsGoHome];
        this.listAllBills = [...enrichedBills];
        this.listBills = [...enrichedBills];

        this.isLoadedAllBill = true;
      });
  }

  /**
  * Tm kim bill mang v
  * @param value gi tr tm kim
  */
  searchBillGoHome(value: string) {
    this.listBills = this.billService.searchBillGoHome(value, this.billsGoHome);
  }


  get countBillTable(): string {
    return this.listAllBills.filter(bill => bill.id_table !== 'go_home').length.toString();
  }

  get countBillGoHome(): string {
    return this.listAllBills.filter(bill => bill.id_table === 'go_home').length.toString();
  }

  getNumberTableByArea(id_area) {
    return this.listAllBills.filter((bill) => bill.id_area === id_area).length;
  }

  /**
   * thit lp giao din gi mn
   * @param type 1 | 2 | 3
   */
  public updateInterface(type, permission) {
    if (this.vhAuth.checkMyPermission(permission)) {
      Promise.all([this.vhComponent.showLoading(this.languageService.translate("Changing interface..."), "current-loading", null, null, false),
      this.vhAuth.updateMyPermission({ default_display_page: permission }),
      ])
        .then(() => {
          this.vhComponent.hideLoading(0);
          setTimeout(() => {
            this.router.navigate(['/cafe/' + type]);
          }, 200);

        });
    }
  }


  observerScale = new ResizeObserver(() => {
    this.adjustScale();
  });

  // Hm thay i zoom  full screen
  adjustScale() {
    const container = this.element.nativeElement.children[0];
    if (!container) return;
    const viewportHeight = window.innerHeight;
    const containerHeight = container.offsetHeight;
    const scaleY = viewportHeight / containerHeight;
    container.style.zoom = scaleY;
    if (container) {
      this.observerScale.observe(container);
    }
  }

  id_selected = 'all';

  handleGetAllBill() {
    if (this.id_selected !== 'all') {
      this.listBills = this.listAllBills;
      this.id_selected = 'all';
    }
  }

  handleGetBillByArea(id_area) {
    if (this.id_selected !== id_area) {
      this.listBills = this.listAllBills.filter((bill) => bill.id_area === id_area);
      this.id_selected = id_area;
    }
  }

  handleGetBillTable() {
    if (this.id_selected !== 'table') {
      this.listBills = this.listAllBills.filter((bill) => bill.id_table !== 'go_home');
      this.id_selected = 'table';
    }
  }

  handleGetBillGoHome() {
    if (this.id_selected !== 'go_home') {
      this.listBills = this.listAllBills.filter((bill) => bill.id_table === 'go_home');
      this.id_selected = 'go_home';
    }
  }

  gotoChoosetable() {
    this.router.navigate(['/cafe/cafe-fnb/choose-table'], { state: { action: 'create-bill' } });
  }

  createBillGoHome() {
    this.selectedAreaID = 'go_home';
    this.selectedTableID = 'go_home';
    this.indexTab = undefined;
    this.filterType = 1;
    this.billService.createBill(this.selectedAreaID, this.selectedTableID, this.searchValueAreaGoHome, this.indexTabArea, this.filterType);
  }

  createBillOnTable(table: any) {
    if (!table.status) {
      this.selectedTableID = table._id;
      this.billService.createBill(this.selectedAreaID, this.selectedTableID, this.searchValueAreaGoHome, this.indexTabArea, this.filterType);
    }
    else {
      const bill = this.listAllBills.find((bill) => bill.id_table === table._id);
      if (bill) {
        this.selectedBill = new VhOrderInvoices(bill, [], this.vhQueryCafe);
        this.selectedTableID = table._id;
        this.areaChanged(table.id_area);
        this.debt = bill.id_customer == 'id_retail' ? false : this.vhQueryCafe.getlocalCustomer(bill.id_customer);
        let dataRestore = { indexTab: this.indexTabArea, searchValue: this.searchValueAreaGoHome, filterType: this.filterType, id_area: this.selectedAreaID };
        if (bill.bill_type == 5) {
          this.router.navigate(['/cafe/cafe-fnb/detail'], { state: { id_table: this.selectedTableID, id_bill: bill._id, dataRestore } });
        }
      }
      else {
        this.vhQueryCafe.updateTable(table._id, { status: false });
      }
    }
  }
}
