import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { VhAlgorithm, VhQuery, VhQueryCafe } from 'ionic-vhframeworks';
import { Component, OnInit, ChangeDetectorRef, Input, SimpleChanges } from '@angular/core';
import { Router } from '@angular/router';
import jsbarcode from 'jsbarcode';
import printJS from 'print-js';
declare var electron: any;
import { Platform } from '@ionic/angular';
import { LanguageService } from 'src/app/cafe/services/language.service';
import { NzModalService } from 'ng-zorro-antd/modal';

@Component({
  selector: 'app-print-one-col',
  templateUrl: './print-one-col.component.html',
  styleUrls: ['./print-one-col.component.scss']
})
export class PrintOneColComponent {
  
  /** Mng object s in */
  @Input() list_dishs: any[];
  @Input() stamp: any;
  @Input() number = 1;
  @Input() tableInfo;
  @Input() totalNumber;

  ratio: number = 3;
  root: any;
  data_show : any[] = [];



  constructor(
    private lang: LanguageService,
    public vhAlgorithm: VhAlgorithm,
    private vhComponent: VhComponent,
    public vhQueryCafe: VhQueryCafe,
    private platform: Platform,
  ) { }



  ngOnChanges({ list_dishs }: SimpleChanges): void {
    if (list_dishs && list_dishs.currentValue) {
      this.data_show = []
      this.onChangeData()
      setTimeout(() => {
        this.printBrowser();
      }, 200);
    }
  }



  /**
   * Tnh tng tin ca sn phm. Nu c topping th tnh c topping
   * @param data sn phm cn tnh tin
   * @returns tng tin ca sn phm 
   */
  getPrice(data) {
    let total = data.price * data.quantity;
    if(data.toppings) {
      total += data.toppings.reduce((sum, topping) => sum + topping.price * topping.quantity, 0);
    }
    return total;
  }



  /**
   * Gi khi thay i d liu list_dishs truyn vo,  cp nht li data_show
   */
  onChangeData() {
    for (let i of this.list_dishs) { 
      for(let j = 0; j < i.quantity; j++) {
        const item = {
          ...i,
          price: this.getPrice(i),
        };
        // Nu l combo th ly tn combo
        if (i.ptype === 5) item.subComboName = this.getComboName(i);
        this.data_show.push(item);
      }
    } 
  }



  /**
   * Ly tn combo
   * @param product sn phm cn ly tn combo 
   * @returns tn combo, nu khng c th tr v chui rng
   */
  getComboName(product): string {
    if(!product.combos) return '';

    let comboName = [];
    for (let combo of product.combos) {
      let cb = `${combo.quantity}x ${combo.name} (${this.getUnit(combo.units, combo.ratio)})`;
      if (combo.lots && combo.id_lotproduct) {
        cb += ` (${this.getLotNumber(combo.lots, combo.id_lotproduct)})`;
      }
      comboName.push(cb);
    }
    return comboName.join(', ');
  }



  /**
   * Ly tn n v theo t l
   * @param units mng n v ca sn phm
   * @param ratio t l cn ly
   */
  getUnit(units: any[], ratio) {
    return this.vhQueryCafe.getUnit_byRatio(units, ratio).unit;
  }

  /**
   * Ly tn l theo id_lot c chn
   * @param lots mng l ca sn phm
   * @param _id id l cn ly
   * @returns tn l
   */
  getLotNumber(lots, _id) {
    let lot = lots.find(item => item._id == _id);
    return lot.lot_number;
  }



  Print() {
    if (this.platform.is("electron")) this.printElectron();
    else this.printBrowser();
  }



  printElectron() {
    let html = document.getElementById("stamp-barcode-electron").innerHTML;
    electron.ipcRenderer.send("print-start", {
      html: html,
      deviceName: this.stamp.printer_name,
    });
    electron.ipcRenderer.on("print-done", (event, data) => {
      if (!data) this.vhComponent.alertMessageDesktop('warning', this.lang.translate('Printing failed'), 3000);
    });
  }



  printBrowser() {
    printJS({
      printable: "stamp-barcode",
      type: "html",
      scanStyles: false,
      maxWidth: this.stamp.width,
      style:
        '@page { margin: 0 !important;padding: 0 !important;}' +
        '.stamp {margin-bottom: 3mm; width: ' + this.stamp.width + 'mm;height: ' + (this.stamp.height - 6) + 'mm;}' +
        '.center-all-content { display: flex;align-items: center;justify-content: center;}'
    });
  }

}
